import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import DemoUrl from '../send_no_send/DemoUrl';
import PosterImage from '../send_no_send/PosterImage';
import ButtonLink from '../send_no_send/ButtonLink';
import WarningMessage from '../warning_message/WarningMessage';

export class SendNoSendContainer extends React.Component {
    render () {
        const { dispatch, history, isReady } = this.props;

        return (
            <StyledContainer>
                {
                    ! isReady &&
                    <WarningMessage>
                        Your video is still being processed. Please do not send the link until this message goes away
                    </WarningMessage>
                }
                <DemoUrl />
                {/*<PosterImage />*/}
                <ButtonLink />
            </StyledContainer>
        );
    }
}

const StyledContainer = styled.div`
    width: 360px;
    height: 360px;
    box-sizing: border-box;
`;

function mapStateToProps({ videos }) {
    let isReady = true;

    Object.keys(videos).forEach(videoKey => {
        let video = videos[videoKey];

        switch (video.state) {
            case 'uploading':
            case 'processing':
                isReady = false;
            break;
        }
    });

    return {
        isReady,
    };
}

export default connect(mapStateToProps)(SendNoSendContainer);


