import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function Error({ video, cancel }) {
    return (
        <ItIsHappening
            className    = 'error'
            normalText   = 'Sorry, something went wrong...'
            actionText   = 'Retry'
            videoState   = { video.state }
            clickHandler = { cancel }
        />
    );
}



