import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import RecorderButton from '../content_studio/RecorderButton';
import TextInput from '../text_input/TextInput';
// states
import Launching from '../content_studio/states/Launching';
import Started from '../content_studio/states/Started';
import Processing from '../content_studio/states/Processing';
import Uploading from '../content_studio/states/Uploading';
import Cancelling from '../content_studio/states/Cancelling';
import VideoDetails from '../content_studio/states/VideoDetails';
import PromptQuestion from '../content_studio/states/PromptQuestion';
import NoVideo from '../content_studio/states/NoVideo';
import NeedDownload from '../content_studio/states/NeedDownload';
import Error from '../content_studio/states/Error';
import WarningMessage from '../warning_message/WarningMessage';


export class VideoContainer extends React.Component {
    constructor(props) {
        super(props);
        this.uploadStart   = this.uploadStart.bind(this);
        this.uploadError   = this.uploadError.bind(this);
        this.uploaded      = this.uploaded.bind(this);
        this.cancel        = this.cancel.bind(this);
        this.retry         = this.retry.bind(this);
        this.launch        = this.launch.bind(this);
        this.start         = this.start.bind(this);
        this.popupControl  = this.popupControl.bind(this);
        this.uploadPercent = this.uploadPercent.bind(this);
    }

    shouldComponentUpdate(nextProps) {
        return this.props.video.state !== nextProps.video.state;
    }

    componentWillReceiveProps(nextProps) {
        const videoState     = this.props.video.state;
        const popupOn        = this.props.popupOn;
        const nextVideoState = nextProps.video.state;

        const shouldCancel = ! this.props.cancelling
            && nextProps.cancelling
            && videoState == 'uploading';

        if (shouldCancel) {
            this.abortDirectUpload();
        }

        if (videoState === 'launching' && nextVideoState === 'started' && popupOn) {
            this.popupControl('HIDE');
        }
        else if (videoState !== 'processing' && nextVideoState === 'processing' && !popupOn) {
            this.popupControl('SHOW');
        }
        else if (videoState !== 'uploading' && nextVideoState === 'uploading' && !popupOn) {
            this.popupControl('SHOW');
        }
        else if (videoState !== 'noVideo' && nextVideoState === 'noVideo' && !popupOn) {
            this.popupControl('SHOW');
        }
        else if (videoState !== 'error' && nextVideoState === 'error' && !popupOn) {
            this.popupControl('SHOW');
        }
    }

    uploadStart(file) {
        const { id } = this.props.video;

        // we lose this.s3Uploader on state transition
        this.activeUploader = this.s3Uploader;

        this.props.dispatch({
            type: 'VIDEO/UPLOADING',
            payload: id
        });
    }

    uploadPercent(progress) {
        const { id } = this.props.video;
        this.props.dispatch({
            type: 'VIDEO/UPDATE_UPLOAD_PROGRESS',
            payload: { id, progress }
        });
    }

    uploadError(error) {
        const { id } = this.props.video;

        // TODO: consume the error message
        console.error(error);

        this.props.dispatch({
            type: 'VIDEO/ERROR',
            payload: id
        });
    }

    uploaded(url, title) {
        const { id, hash }  = this.props.video;
        this.activeUploader = null;

        this.props.dispatch({
            type: 'UPLOADED/VIDEO',
            payload: { id, hash, url, title }
        });
    }

    abortDirectUpload() {
        if (this.activeUploader) {
            this.activeUploader.abort();
            this.activeUploader = null;
        }
    }

    cancel() {
        const { video, dispatch } = this.props;

        this.abortDirectUpload();

        dispatch({
            type: 'VIDEO/DELETE',
            payload: video.id
        });
    }

    retry() {
        const { video, dispatch } = this.props;
        dispatch({
            type: 'VIDEO/NO_VIDEO',
            payload: video.id
        });
    }

    launch() {
        const { video, dispatch } = this.props;
        dispatch({
            type: 'VIDEO/LAUNCHING',
            payload: video.id
        });
    }

    start(result) {
        const { video, dispatch } = this.props;

        let action;
        if (result.success) {
            action = 'VIDEO/STARTED';
        }
        else if (result.protocol) {
            action = 'VIDEO/PROMPT_QUESTION';
        }
        else {
            action = 'VIDEO/NEED_DOWNLOAD';
        }

        dispatch({ type: action, payload: video.id });
    }

    popupControl(action) {
        this.props.dispatch({
            type: `POPUP/${action}`
        });
    }

    render() {
        const { video, dispatch, environment, canCreateDemos }  = this.props;
        const StyledVideoContainer = styled.div`
            width: 100%;
        `;

        if ( ! canCreateDemos) {
            return (
                <WarningMessage>
                    To use this feature your account owner needs to enable the personal demos setting.
                </WarningMessage>
            );
        }

        return (
            <StyledVideoContainer className='video-container'>
            {
                video.state === 'error' &&
                <Error
                    video  = {video}
                    cancel = {this.retry}
                />
            }
            {
                video.state === 'needDownload' &&
                <NeedDownload
                    video  = {video}
                    cancel = {this.retry}
                />
            }
            {
                video.state === 'deleting' &&
                <Cancelling video={video} />
            }
            {
                video.state === 'uploading' &&
                <Uploading
                    video  = {video}
                    cancel = {this.cancel}
                />
            }
            {
                video.state === 'launching' &&
                <Launching video={video} />
            }
            {
                video.state === 'started' &&
                <Started video={video} />
            }
            {
                video.state === 'videoDetails' &&
                <VideoDetails
                    video  = {video}
                    cancel = {this.cancel}
                />
            }
            {
                video.state === 'promptQuestion' &&
                <PromptQuestion
                    video  = {video}
                    cancel = {this.retry}
                />
            }
            {
                video.state === 'processing' &&
                <Processing
                    video  = {video}
                    cancel = {this.cancel}
                />
            }
            {
                video.state === 'noVideo' &&
                <NoVideo
                    video         = {video}
                    environment   = {environment}
                    cancel        = {this.cancel}
                    launch        = {this.launch}
                    start         = {this.start}
                    uploadStart   = {this.uploadStart}
                    uploadError   = {this.uploadError}
                    uploaded      = {this.uploaded}
                    uploadPercent = {this.uploadPercent}
                    uploaderRef   = {ctrl => this.s3Uploader = ctrl}
                />
            }
            </StyledVideoContainer>
        );
    }
}

function mapStateToProps(state) {
    return {
        environment:    state.environment,
        canCreateDemos: state.userPermissions.canCreateDemos,
        popupOn:        state.popupOn,
        cancelling:     state.world.cancelling,
    };
}

export default connect(mapStateToProps)(VideoContainer);



