export function hammerIn(settings) {
    return {
        language: settings.language,

        contactInfo: {
            phone: settings.phone,
            liveChatLink: settings.live_chat_url,
        },

        colors: {
            bottomBar: { hex: settings.theme_pri_color },
            text: { hex: settings.theme_sec_color },
            callToAction: { hex: settings.theme_btn_color },
        },

        buyerBoard: {
            enabled: settings.avfc_on,
            showName: settings.avfc_viewer_name_on,
            showTitle: settings.avfc_viewer_title_on,
        },

        cta: {
            freeTrial: {
                enabled: settings.cta_trial_on,
                link: settings.cta_trial_url,
                skipLeadGate: settings.cta_trial_lead_form_off,
            },

            contactMe: {
                enabled: settings.cta_contact_on
            },

            buyNow: {
                enabled: settings.cta_buy_now_on,
                link: settings.cta_buy_now_url,
            },

            customButton: {
                enabled: settings.cta_button_on,
                label: settings.cta_button_label,
                use: settings.cta_button_action_on ? 'external' : 'default',
                link: settings.cta_button_action_url,
            }
        },


        analytics: {
            vendor: settings.web_analytics_vendor_id,
            code: settings.web_analytics_code,
            adobeConstraint: settings.web_adobe_constraint_on,
        },

        adwords: {
            id: settings.web_google_conv_id,
            label: settings.web_google_conv_label,
        },

        images: {
            logo: settings.logo_image,
            favicon: settings.favicon,
            background: settings.theme_bgd_image,
        },

        leadgate: settings.cta_signup_placement_id,
        sharegate: settings.cta_share_placement_id,
        theme: settings.theme_id,
    };
}

export function hammerOut(payload) {
    return {
        language:                payload.language,
        phone:                   payload.contactInfo.phone,
        live_chat_url:           payload.contactInfo.liveChatLink,
        theme_id:                payload.theme,
        theme_bgd_image:         payload.theme.image,
        theme_pri_color:         payload.colors.bottomBar.hex,
        theme_sec_color:         payload.colors.text.hex,
        theme_btn_color:         payload.colors.callToAction.hex,
        cta_signup_placement_id: payload.leadgate,
        cta_share_placement_id:  payload.sharegate,
        avfc_on:                 payload.buyerBoard.enabled,
        avfc_viewer_name_on:     payload.buyerBoard.showName,
        avfc_viewer_title_on:    payload.buyerBoard.showTitle,
        cta_contact_on:          payload.cta.contactMe.enabled,
        cta_trial_on:            payload.cta.freeTrial.enabled,
        cta_trial_url:           payload.cta.freeTrial.link,
        cta_trial_lead_form_off: payload.cta.freeTrial.skipLeadGate,
        cta_buy_now_on:          payload.cta.buyNow.enabled,
        cta_buy_now_url:         payload.cta.buyNow.link,
        cta_button_on:           payload.cta.customButton.enabled,
        cta_button_label:        payload.cta.customButton.label,
        cta_button_action_on:    payload.cta.customButton.use === 'external' ? true : false,
        cta_button_action_url:   payload.cta.customButton.link,
        web_analytics_vendor_id: payload.analytics.vendor,
        web_analytics_code:      payload.analytics.code,
        web_adobe_constraint_on: payload.analytics.adobeConstraint,
        web_google_conv_id:      payload.adwords.id,
        web_google_conv_label:   payload.adwords.label,
        logo_image:              payload.images.logo,
        favicon:                 payload.images.favicon,
        theme_bgd_image:         payload.images.background,
    };

    // {
    //     id: "5",
    //     user_id: "2",
    //     for_demo_type: "single",
    //     profile_name: "JIT",
    //     updated: "2017-05-13 19:06:05",
    //     is_personal: true,
    //     language: "en",
    //     title: null,
    //     description: null,


    //     hook_offer_off: false,
    //     hook_offer_text: null,



    //     cta_share_after_seconds: "0",
    //     cta_share_after_videos: "0",
    // };
}


