import React from 'react';
import { connect } from 'react-redux';
import { Field, formValueSelector } from 'redux-form';
import Checkbox from '../checkbox/Checkbox';


export function BuyerBoardGroup({ isEnabled }) {
    return (
        <div className='buyerboard-group'>
            <div className='field-label'>BuyerBoard</div>
            <div>
                <Checkbox name='buyerBoard.enabled' label='Enable BuyerBoard'/>
            </div>
            {
                isEnabled &&
                <div>
                    <Checkbox name='buyerBoard.showName' label='Show Viewer Name'/>
                    <Checkbox name='buyerBoard.showTitle' label='Show Viewer Title'/>
                </div>
            }
        </div>
    );
}

const selector = formValueSelector('settingsForm');

const mapStateToProps = state => ({
    isEnabled: selector(state, 'buyerBoard.enabled')
});

export default connect(mapStateToProps)(BuyerBoardGroup);



