import React from 'react';
import styled from 'styled-components';
import Autocomplete from 'react-autocomplete';


export default function Organization({ selectHandler, searchHandler, items, autoVal, name, placeholder}) {
    return(
        <StyledOrganization className='organization'>
            <Autocomplete
                inputProps   = {{name, placeholder}}
                value        = {autoVal}
                onChange     = {searchHandler}
                onSelect     = {selectHandler}
                items        = {items}
                getItemValue = {(item) => item.name}
                renderItem   = {(item, isHighlighted) => (
                    <div
                        key = {item.id}
                        id  = {item.id}
                        className = "option"
                    >
                        {item.name}
                    </div>
                )}
            />
            {
                ! autoVal && <span className='required-field-asterisk'>*</span>
            }
        </StyledOrganization>
    );
}

const StyledOrganization = styled.div`
    display: flex;
    align-items: center;
    position: relative;
    z-index: 50000;

    div {
        .option{
            color:#767676;
            padding: 5px 0 5px 0;
            text-transform: capitalize;
            &:hover{
                cursor: pointer;
                background-color: #8c8c8c;
                color: #fff;
            }
        }
    }

    input{
        width: 300px;
        color: #767676;
        height: 35px;
        box-sizing: border-box;
        padding-left: 8px;
        border: 1px solid #ECECEC;

        &:focus {
            outline: 0;
        }

        &::placeholder {
            color: #A8A9A9;
        }
    }

    .required-field-asterisk {
        position: absolute;
        top: 5px;
        right: 8px;
        font-size: 9px;
        color: #FC3C1D;
    }
`;



