import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { Store } from 'react-chrome-redux';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { shakeElement } from '../../helpers/tweens';
import PrivateContainer from '../../components/private_container/PrivateContainer';


// the shakes saga makes this phonecall (e.g. on form submit fail)
chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    if (request.action === 'shake') {
        shakeElement(request.selector);
    }
});

const proxyStore = new Store({
    portName: 'JIDPort'
});

function RootComponent() {
    return (
        <Router>
            <Route path='/src/action/popup/popup.html' component={PrivateContainer}/>
        </Router>
    );
}

render(
    <Provider store={proxyStore}><RootComponent /></Provider>,
    document.getElementById('reactApp')
);



