import mime from 'mime-types';

/**
 * Validates the file type in accordance with the input accept
 * attribute (see https://www.w3schools.com/tags/att_input_accept.asp)
 *
 * Currently, supports only mime types. No explicit extensions!
 */
export default function acceptFile(file, accept) {
    let   result   = false;
    const mimeType = mime.lookup(file);

    if (mimeType) {
        const allTypes = accept.split(',');
        const wildType = mimeType.replace(/([^/]+\/).*/, "$1*");

        result = !! [mimeType, wildType].find(
            type => allTypes.indexOf(type) != -1
        );
    }

    return result;
}


