import React from 'react';


export default function Plus({clickHandler}) {
    return (
        <div className = 'plus-button'>
            <svg onClick = { clickHandler } width="40px" height="40px" viewBox="0 0 40 40" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <g id="design" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                    <g id="input-field" transform="translate(-773.000000, -200.000000)">
                        <g id="Group-8" transform="translate(589.000000, 80.000000)">
                            <g id="icon-wizard-plus" transform="translate(184.000000, 120.000000)">
                                <circle id="shapeToFill" fill="#fff" cx="20" cy="20" r="20"></circle>
                                <g id="plus" transform="translate(13.000000, 13.000000)" fillRule="nonzero" fill="#FF8B1E">
                                    <polygon id="Shape" points="14.00004 7.99995 8.00004 7.99995 8.00004 13.99995 6.00004 13.99995 6.00004 7.99995 0 7.99995 0 5.99995 6.00004 5.99995 6.00004 0 8.00004 0 8.00004 5.99995 14.00004 5.99995"></polygon>
                                </g>
                            </g>
                        </g>
                    </g>
                </g>
            </svg>
        </div>
    );
}










