import React from 'react';
import styled from 'styled-components';


/*
Pieces currently missing:

State:
    All sent demos organized by sendgroup
    Resend link (sendgroup)
    Invitees per demo
        Status of each invitee (created/invited)
        Number of views per invitee
        Resend link per invitee

Component State:
    Maybe to keep around the sorting/filtering options

Actions:
    maybe none are needed
*/
export default function TrackDemosContainer({ }) {
    return (
        <StyledContainer>
            <div className='stats'> {/* make it its own component */}
                <div className='demos-sent'>0</div>DEMOS SENT &nbsp; | &nbsp;
                <div className='viewed'>0</div>VIEWED &nbsp; | &nbsp;
                <div className='viewers'>0</div>VIEWERS &nbsp; | &nbsp;
                <div className='view-rate'>0%</div>VIEW RATE
            </div> <br/>
            <div className='sends'>
                <div className='controls'> {/* make it its own component */}
                    <div className='sort'>sort</div>
                    <div className='mag-glass'>mag glass</div>
                </div> <br/>
                <div className='sendgroups'>
                    <div className='sendgroup-1'> {/* make it its own component */}
                        <div className='invitation-data'>
                            <div className='demo'>Mocrotosh <br /> Demo Name</div>
                        </div> <br/>
                        <div className='invitees'>
                            <div className='invitee'> {/* make it its own component */}
                                Nikolai Ramonski icons
                            </div> <br/>
                            <div className='invitee'>
                                Jusev Reniv icons
                            </div> <br/>
                        </div>
                    </div>

                    <div className='sendgroup-2'> {/* make it its own component */}
                        <div className='invitation-data'>
                            <div className='demo'>Company 2 <br /> Demo Name</div>
                        </div> <br/>
                        <div className='invitees'>
                            <div className='invitee'> {/* make it its own component */}
                                Charles Dickson
                            </div> <br/>
                            <div className='invitee'>
                                Mike Miller
                            </div> <br/>
                        </div>
                    </div>
                </div>
            </div>
        </StyledContainer>
    );
}

const StyledContainer = styled.div`
    height: 100%;
    width: 100%;
    padding: 15px;
    box-sizing: border-box;

    .stats {
        display: flex;
    }
    .controls {
        display: flex;
    }
    .invitation-data {
        font-weight: bold;
    }
`;



