import React from 'react';
import styled from 'styled-components';


const styles = {
    error: {
        color: '#9D5656',
        backgroundColor: '#F7CFCF',
    }
};

export default function Flash({ type, message }) {
    const StyledFlash = styled.div`
        height: 45px;
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        color: ${styles[type].color};
        background-color: ${styles[type].backgroundColor}
    `;


    return (
        <StyledFlash className='flash-message'>
            { message }
        </StyledFlash>
    );
}



