import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import CloseIcon from '../svg_react/close_icon/CloseIcon';
import SettingsForm from '../settings_form/SettingsForm';


function toggleSpinner(dispatch, bit) {
    dispatch({
        type: `SPINNER/${bit}`,
        payload: 'settings',
    });
}

export class SettingsContainer extends React.Component {
    render() {
        const { history, match } = this.props;
        return (
            <StyledContainer>
                <div className='nav'>
                    <div className='tab'>SETTINGS</div>
                    <CloseIcon clickHandler={() => history.push(this.props.latestLocation)} />
                </div>
                <div className='form-container'>
                    <SettingsForm history={history} />
                </div>
            </StyledContainer>
        );
    }
}

export default connect(state => ({
    latestLocation: state.latestLocation
}))(SettingsContainer);

const StyledContainer = styled.div`
    padding-top: 5px;

    .nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 12px 12px; 0;

        .tab {
            outline: none;
            text-decoration: none;
            color: #8C8C8C;
            font-weight: bold;
        }
    }
`;



