export function shakeElement(selector) {
    TweenMax.fromTo(
        document.querySelectorAll(selector),
        0.3,
        { x: -1 },
        {
            x: 1,
            clearProps: 'x',
            ease: RoughEase.ease.config({
                strength: 8,
                points: 20,
                template: Linear.easeNone,
                randomize: false
            })
        }
    );
}



