import React from 'react';
import ReactS3Uploader from 'react-s3-uploader';
import styled from 'styled-components';
import withProtocol from '../../helpers/withProtocol';
import raise from '../../helpers/raise';
import acceptFile from '../../helpers/acceptFile';
import { StyledContentStudioButton } from '../content_studio/StyledContentStudioButton';
import UploadCloud from '../svg_react/content_studio/UploadCloud';

/**
 * @param {*} environment, injected by parent
 * @param {*} accept, injected by parent (refers to filetypes)
 * @param {*} onUploaded, handler of uploaded url
 * @param {*} change, optional, needed only for redux-form
 * @param {*} input, optional, will exist when using with redux-form
 * @param {*} uploadRoot, optional
 * @return component
 */
export default class UploadArea extends React.Component {
    constructor(props) {
        super(props);
        this.uploadStart    = this.uploadStart.bind(this);
        this.uploadProgress = this.uploadProgress.bind(this);
        this.uploadError    = this.uploadError.bind(this);
        this.uploadFinish   = this.uploadFinish.bind(this);
        this.scrubFilename  = this.scrubFilename.bind(this);
        this.acceptsImages  = this.acceptsImages.bind(this);
    }

    uploadStart(file, next) {
        const { onUploadStart, accept } = this.props;

        if ( ! acceptFile(file.name, accept)) {
            alert(`Unsupported file type: ${file.name}`);
        }
        else {
            raise(onUploadStart, file);
            next(file);
        }
    }

    uploadProgress(percent, status, file) {
        const { onUploadPercent } = this.props;
        raise(onUploadPercent, percent/100);
    }

    uploadError(error, file) {
        const { onUploadError } = this.props;
        raise(onUploadError, error);
    }

    uploadFinish(result, file) {
        const { change, input, onUploaded } = this.props;
        const url = result.signedUrl.split('?')[0];

        if (input) {
            // This applies when using <UploadArea /> with redux-form.
            // `input` is available automatically through props
            // `change` is a redux-form action dispatcher we pass down manually
            raise(change, input.name, url);
        }

        raise(onUploaded, url, file.name);
    }

    scrubFilename(filename) {
        const cleanFilename = filename.replace(/[^\w\d_\-\.]+/ig, '');
        return this.props.uploadRoot
            ? `${this.props.uploadRoot}${cleanFilename}`
            : cleanFilename;
    }

    acceptsImages() {
        return !!(/image\//.test(this.props.accept));
    }

    render() {
        const { input, environment, accept, uploadRoot, uploaderRef } = this.props;
        const authToken  = window.localStorage.getItem('JID_authedUserApiKey');

        return (
            <StyledUploadButton>
                <label>
                    {
                        this.acceptsImages() &&
                        <div className='thumbnail'><img src={ withProtocol(input.value) } /></div>
                    }
                    {
                        this.acceptsImages()
                        ? <div className='button'>Upload</div>
                        : <StyledContentStudioButton  width='188px' ><UploadCloud/>Add New Video</StyledContentStudioButton>
                    }

                    <ReactS3Uploader
                        signingUrl           = '/files/sign'
                        signingUrlMethod     = 'GET'
                        signingUrlHeaders    = {{ 'Authorization': `Bearer ${authToken}` }}
                        contentDisposition   = 'auto'
                        uploadRequestHeaders = {{ }}
                        accept               = { accept }
                        server               = { environment }
                        preprocess           = { this.uploadStart }
                        onProgress           = { this.uploadProgress }
                        onError              = { this.uploadError }
                        onFinish             = { this.uploadFinish }
                        scrubFilename        = { this.scrubFilename }
                        ref                  = { uploaderRef }
                    />
                    <input type='hidden' { ...input } />
                </label>
            </StyledUploadButton>
        );
    }
}

const StyledUploadButton = styled.div`
    label {
        overflow: hidden;
        position: relative;

        .thumbnail {
            width: 142px;
            margin: 5px 0 5px 0;

            img {
                max-width: 100%;
                height: auto;
            }
        }

        .button {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 142px;
            height: 45px;
            border: none;
            border-radius: 2px;
            cursor: pointer;
            color: white;
            background-color: #01B3E4;
            font-size: 14px;
        }

        input[type='file'] {
            opacity: 0;
            position: absolute;
            right: 0;
            top: 0;
        }
    }
`;


