import React from 'react';
import styled from 'styled-components';
import { TweenMax, Power2, TimelineLite, Elastic, Bounce, Back } from 'gsap';
import { Field, reduxForm } from 'redux-form';
import TextInput from '../text_input/TextInput';
import Button from '../button/Button';
import validate from './validate';
import EnvironmentSelector from '../environment_selector/EnvironmentSelector';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/bufferWhen';
import 'rxjs/add/operator/debounceTime';


export class LoginForm extends React.Component{
    constructor(props) {
        super(props);
        this.state = { envSelector: false };
        this.toggleEnvSelector = this.toggleEnvSelector.bind(this);
    }

    toggleEnvSelector() {
        this.setState({ envSelector: !this.state.envSelector });
    }

    componentDidMount() {
        const staggerable = document.querySelectorAll('.stagger-me-1');
        const image       = document.querySelector('img');
        const clickStream = Observable.fromEvent(image, 'click');

        clickStream
            .bufferWhen(() => clickStream.debounceTime(250))
            .map(arr => arr.length)
            .filter(len => len === 3)
            .subscribe(() => this.toggleEnvSelector());

        TweenMax.staggerFrom(staggerable, 0.5, {
            y:       75,
            opacity: 0,
            delay:   1.5,
        }, 0.2);
    }

    render() {
        const { handleSubmit, pristine, reset, submitting, dispatch } = this.props;

        function submit(values) {
            dispatch({
                type: 'LOGIN_ATTEMPT',
                payload: values
            });
        }

        return (
            <div>
                <StyledLoginForm onSubmit={handleSubmit(submit)}>
                    <div className = 'stagger-me-1'>
                        <Field
                            className    = 'email-input'
                            name         = 'email'
                            type         = 'text'
                            component    = {TextInput}
                            placeholder  = 'Email'
                        />
                    </div>
                    <div className = 'stagger-me-1'>
                        <Field
                            className    = 'password-input'
                            name         = 'password'
                            type         = 'password'
                            component    = {TextInput}
                            placeholder  = 'Password'
                        />
                    </div>
                    <div className = 'stagger-me-1'>
                        <Button
                            type      = 'submit'
                            disabled  = {submitting}
                            width     = '100%'
                            height    = '45px'
                            margin    = '20px 0 0 0'
                        >
                            LOGIN
                        </Button>
                    </div>
                    <div className = 'stagger-me-1'>
                        <StyledCreateAccount>
                            Don't have an account?
                            <a href="http://www.goconsensus.com/snap" target="_blank">Create account</a>
                        </StyledCreateAccount>
                    </div>
                </StyledLoginForm>
                {
                    this.state.envSelector &&
                    <EnvironmentSelector dispatch={dispatch} />
                }
            </div>
        );
    }
}

const StyledLoginForm = styled.form`
    width: 100%;
    height: 
`;

const StyledCreateAccount = styled.div`
    color: #cfcfcf;
    text-align: center;
    margin-top: 10px;

    a {
        color: #bdbdbd;
        text-decoration: none;
        padding-left: 4px;
    }
`;

export default reduxForm({
    form: 'loginForm',
    validate,
})(LoginForm);



