import React from 'react';
import { Field, reduxForm } from 'redux-form';
import TextInput from '../text_input/TextInput';
import normalizePhone from '../../helpers/normalizePhone';


export default function ContactInfoGroup({ }) {
    return (
        <div className='contact-group'>
            <div className='field-label'>Contact Information</div>
            <Field name='contactInfo.phone' component={TextInput} type='tel' placeholder='Phone' normalize={normalizePhone}/><br/>
            <Field name='contactInfo.liveChatLink' component={TextInput} type='text' placeholder='Live chat link' />
        </div>
    );
}



