import React from 'react';
import styled, { keyframes } from 'styled-components';

export default class RecipientInput extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fieldEmpty: true,
        };
        this.changeHandler = this.changeHandler.bind(this);
        this.isFieldEmpty  = this.isFieldEmpty.bind(this);
    }

    componentDidMount() {
        this.isFieldEmpty();
    }

    isFieldEmpty() {
        const fieldValue = document.querySelector(`.person_${this.props.compId}`).value.trim();

        if (fieldValue) {
            this.setState({ fieldEmpty: false });
        }
        else {
            this.setState({ fieldEmpty: true });
        }
    }

    changeHandler() {
        this.props.updateHandler();
        this.isFieldEmpty();
    }

    render() {
        const {
            compId,
            type,
            additionalClass,
            placeholder,
            defaultValue,
            updateHandler,
            name,
            width,
            required,
        } = this.props;

        return (
            <StyledRecipient className='text-input' width={width}>
                <input
                    id           = {compId}
                    className    = {additionalClass}
                    onChange     = {this.changeHandler}
                    placeholder  = {placeholder}
                    defaultValue = {defaultValue}
                    name         = {name}
                    type         = 'text'
                    className    = {`person_${compId}`}
                />
                { this.state.fieldEmpty && required && <span className='required-field-asterisk'>*</span> }
            </StyledRecipient>
        );
    }
}

const kf = keyframes`
    to {
        opacity: 1;
    }
`;

const StyledRecipient = styled.div`
    display: inline-block;
    padding: 0 5px 0 5px;
    width: ${props => props.width ? props.width : '20%'};
    position: relative;

    .required-field-asterisk {
        position: absolute;
        top: 5px;
        right: 8px;
        font-size: 9px;
        color: #FC3C1D;
    }

    input {
        border: none;
        border-bottom: thin solid;
        border-color: #bababa;
        color: #767676;
        height: 36px;
        width: 100%;
        box-sizing: border-box;

        &:focus {
            outline: 0;
        }

        &::placeholder {
            color: #A8A9A9;
        }
    }
`;



