import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Recipient from '../recipient/Recipient';
import User from '../svg_react/user/User';
import PeopleBalloon from '../svg_react/people/People';
import Plus from '../svg_react/plus-sign/Plus'; 
import TextInput from '../text_input/TextInput';
import Button from '../button/Button';
import Organization from '../organization/Organization';

import emailRequired from '../../helpers/emailRequired';

export class PeopleContainer extends React.Component 
{
    constructor(props) {
        super(props);

        this.handleAutocompleteSearch = this.handleAutocompleteSearch.bind(this);
        this.selectSearchResult       = this.selectSearchResult.bind(this);
        this.dispatchHandler          = this.dispatchHandler.bind(this);
        this.handleDelete             = this.handleDelete.bind(this);
        this.updateFieldsState        = this.updateFieldsState.bind(this);
   }
    
    componentWillMount() {
        this.props.dispatch({
            type: 'ORGANIZATIONS/GET_RECENT'
        });
    }

    handleAutocompleteSearch(e) {
        let value = e.target.value;
        if (value.length == 0) {
            this.props.dispatch({
                type: 'ORGANIZATIONS/CLEAR_SEARCH_RESULTS'
            });
        } else {
            this.props.dispatch({
                type: 'ORGANIZATIONS/UPDATE_SEARCH_TERM',
                payload: value
            });
        }
    }

    selectSearchResult(value) {
        this.props.dispatch({
            type: 'ORGANIZATIONS/PICK_ORGANIZATION',
            payload: value
        });        
    }

    dispatchHandler() {
        this.props.dispatch({
            type: 'RECIPIENTS/CREATE_IN_STATE'
        });
    }

    handleDelete(form_id, id_invited) {
		let fromServer = false;
        let actionType = (id_invited == '' || ! id_invited)
            ? 'RECIPIENTS/DELETE_FROM_STATE'
            : 'RECIPIENTS/DELETE';

        this.props.dispatch({
            type:    actionType,
            payload: {form_id, id_invited, fromServer}
        });
    }

    updateFieldsState(form_id) {
        let elemVal = name => document.getElementById(`${form_id}_${name}`).value.trim();

        let fname      = elemVal('fname');
        let lname      = elemVal('lname');
        let email      = elemVal('email');
        let title      = elemVal('title');
        let id_invited = elemVal('invited');

        let recipData = { fname, lname, email, title, id_invited, form_id };

        this.props.dispatch({
            type: 'RECIPIENTS/UPDATE_TO_STATE',
            payload: recipData
        });
    }

    render() {
        const { props } = this;
        const { recipients, context } = props;

        let meta = {
            touched: false,
            error: false,
            warning: false
        };

         let plusClass = context != 'gmail'
            ? 'plus-row'
            : 'none';

        return (
            <StyledContainer>
                <div className='smart-input-area'>
                    <Organization
                        name          = 'organization'
                        placeholder   = 'Organization Sending To'
                        autoVal       = { props.organization }
                        searchHandler = { this.handleAutocompleteSearch }
                        selectHandler = { this.selectSearchResult }
                        items         = { props.organizations }
                    />
                </div>
                <br/>
                <div className='people'>
                    {
                        recipients.map(
                            (person, i) => (
                                <Recipient
                                    key             = {person.form_id}
                                    formId          = {person.form_id}
                                    invitedID       = {person.id_invited}
                                    fname           = { person.fname }
                                    lname           = { person.lname }
                                    email           = { person.email.indexOf('notconsensus.fake') == -1 ? person.email : '' }
                                    title           = { person.title }
                                    hasError        = { person.failed !== null ? person.failed : false }
                                    count           = { recipients.length }
                                    updateMethod    = { () => this.updateFieldsState(person.form_id) }
                                    deleteRecipient = { () => this.handleDelete(person.form_id, person.id_invited) }
                                    emailRequired   = { props.emailRequired }
                                />
                            )
                        )
                    }

                    <div className='required-field'><span >* </span> Required Field</div>
                </div>

                <div className={plusClass} >
                    <Plus clickHandler = { this.dispatchHandler } />
                </div>
            </StyledContainer>
        );
    }
}

const StyledContainer = styled.div`
    box-sizing: border-box;
    width: 360px;
    min-height: 360px;
    scroll: none;

    .smart-input-area {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .people {
        max-height: 320px;
        overflow-x: hidden;
        overflow-y: auto;
    }

    .required-field {
        font-size: 9px;
        color: #8C8C8C;

        span {
            color: #FC3C1D;
        }
    }

    .none {
        display: none;
    }

    .plus-row {
        text-align: right;

        svg {
            cursor: pointer;
            display:inline-block;
            transition: all 0.3s 0.01s ease;
            fill:#fff;

            &#plus{
                transition: all 0.3s 0.01s ease;
                fill: #ff8b1e;
            }
            &:hover{
                transform: scale(1.25);
                fill: #111;
            }
        }
    }
`;

const mapStateToProps = (state, ownProps) => {
    return {
        recipients: state.recipients.recipientList,
        organization: state.recipients.organization,
        organizations: state.organizations.results,
        context: state.context,
        emailRequired: emailRequired(state.viewerPrivacy)
    };
};

export default connect(mapStateToProps)(PeopleContainer);



