import React from 'react';
import { connect } from 'react-redux';
import { Route } from 'react-router-dom';
import styled from 'styled-components';

import SettingsContainer from '../settings_container/SettingsContainer';
import MainContainer from '../main_container/MainContainer';
import Header from '../header/Header';
import WarningMessage from '../warning_message/WarningMessage';


export class AppContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            windowHeight: 0,
        };

        // uncomment to debug location problems:
        // this.props.history.listen(location => {
        //     console.log(location.pathname);
        // });
    }

    componentDidMount() {
        const windowHeight = window.innerHeight;
        this.setState({ windowHeight });
        this.props.history.push(this.props.latestLocation);
    }

    dismissColorPickers() {
        const pickerSave = document.querySelector('.colorpicker-save-area');

        if (pickerSave) {
            const event = new Event('dismissColorPickers');
            pickerSave.dispatchEvent(event);
        }
    }

    clickHandler() {
        this.dismissColorPickers();
    }

    componentWillMount() {
        this.props.dispatch({
            type: 'SETTINGS/REFRESH'
        });

        this.props.dispatch({
            type: 'PERMISSIONS/REFRESH'
        });

        this.props.dispatch({
            type: 'VIDEOS/REFRESH'
        });

        this.props.dispatch({
            type: 'VIEWER_PRIVACY/REFRESH'
        });

        let storedGmailRecipients = localStorage.getItem('JID_gmail');

        if( storedGmailRecipients ){
            localStorage.removeItem('JID_gmail');

            this.props.dispatch({
                type: 'RECIPIENTS/FETCH_GMAIL',
                payload: JSON.parse(storedGmailRecipients)
            });
        }
        else {
            this.props.dispatch({
                type: 'CONTEXT/GMAIL',
                payload: 'ext'
            });
        }
    }

    render() {
        const { match, canSendDemos } = this.props;

        return (
            <StyledContainer className='app-container' windowHeight={this.state.windowHeight} onClick={this.clickHandler.bind(this)}>
            {
                canSendDemos
                ?
                    <div className='routes-container'>
                        <Route path={`${match.url}`}          component={Header} /> {/* this always matches so always displays */}
                        <Route path={`${match.url}/settings`} component={SettingsContainer} />
                        <Route path={`${match.url}/main`}     component={MainContainer} />
                    </div>
                :
                    <WarningMessage>
                        You need permission to send demos to use this tool
                    </WarningMessage>
            }
            </StyledContainer>
        );
    }
}

function mapStateToProps(state) {
    return {
        canSendDemos: state.userPermissions.canSendDemos,
        latestLocation: state.latestLocation,
    };
}

export default connect(mapStateToProps)(AppContainer);

const StyledContainer = styled.div`
    box-sizing:       border-box;
    background-color: #F6F6F6;
    height:           ${props => props.windowHeight + 'px'};
`;



