import React from 'react';
import { Route, NavLink } from 'react-router-dom';
import styled from 'styled-components';
import TrackDemosContainer from '../track_demos_container/TrackDemosContainer';
import SendDemoContainer from '../send_demo_container/SendDemoContainer';


// the 'main' part of the app (what we see when we are not on the settings page)
// defaults to track demos
// always showing the 'track' and 'send' tabs on top
export default class MainContainer extends React.Component {
    render() {
        const { match } = this.props;

        return (
            <StyledContainer className='main-container'>
                {/*<div className='nav-main'>
                    <NavLink to={`${match.url}/track`} className='main-tab'>TRACK DEMOS</NavLink>
                    <NavLink to={`${match.url}/send`} className='main-tab'>SEND A DEMO</NavLink>
                </div>*/}

                <div className='main-content'>
                    {/*<Route path={`${match.url}/track`} component={TrackDemosContainer}></Route>*/}
                    <Route path={`${match.url}/send`} component={SendDemoContainer}></Route>
                </div>
            </StyledContainer>
        );
    }
}

const StyledContainer = styled.div`
    .nav-main {
        display: flex;

        .main-tab {
            outline: none;
            text-decoration: none;
            color: #8C8C8C;
            padding: 12px;
            margin: 0 24px 0 0;
            border-bottom: 2px solid white;
            transition: all 0.3s 0.01s ease;

            &.active {
                font-weight: bold;
                border-bottom: 3px solid #01B3E4;
                transition: all 0.3s 0.01s ease;
            }
        }
    }
`;



