import React, { Component } from 'react';
import { NavLink, Route, Redirect } from 'react-router-dom';
import styled from 'styled-components';

// make demo balloon
import MakeDemo from '../svg_react/make_demo/MakeDemo';
import MakeDemoWorking from '../svg_react/make_demo/MakeDemoWorking';
import MakeDemoDone from '../svg_react/make_demo/MakeDemoDone';

// people balloon
import PeopleBalloon from '../svg_react/people/People';
import PeopleCheckBalloon from '../svg_react/people/PeopleCheck';

// send balloon
import SendNot from '../svg_react/send_not/SendNot';
import ProgressRing from '../progress_ring/ProgressRing';


function MakeBalloonWithProgress ({ videoState, demoStepComplete, history }) {
    let CorrectIcon;

    let isProcessing = (videoState == 'processing' || videoState == 'uploading');
    let notStarted   = ! demoStepComplete && ! isProcessing;
    let complete     = demoStepComplete && ! isProcessing;
    let onFirstPage  = /send\/make/.test(history.location.pathname);

    if ( onFirstPage && ! complete ) {
        CorrectIcon = MakeDemo;
    }
    else if ( isProcessing ) {
        CorrectIcon = MakeDemoWorking;
    }
    else if ( complete ) {
        CorrectIcon = MakeDemoDone;
    }
    else { //shouldn't ever be possible, but at least we have a safety net right?
        CorrectIcon = MakeDemo;
    }

    return (
        <div style={{position:'relative'}}>
            <CorrectIcon size='40' />
            <ProgressRing
                size         = '44'
                positionTop  = '-2'
                positionLeft = '-2'
            />
        </div>
    );
}

function PeopleBalloonWithProgress({ peopleStepComplete }) {
    let CorrectIcon = peopleStepComplete
        ? PeopleCheckBalloon
        : PeopleBalloon;

    return (<CorrectIcon size='40' />);
}

export default class SendDemoBubbles extends Component {
    render() {
        const {
            match,
            history,
            demoStepComplete,
            peopleStepComplete,
            allowNavToPeople,
            allowNavToSend,
            videoState,
        } = this.props;

        let isCurrentlyWorking = videoState == 'uploading' || videoState == 'processing';

        let inactivePeopleColor = peopleStepComplete
            ? '#F6F6F6'
            : '#BABABA';

        let activePeopleColor = peopleStepComplete
            ? '#FFFFFF'
            : '#01B3E4';

        let inactiveMakeColor = '#F6F6F6';

        let activeMakeColor = ! demoStepComplete || isCurrentlyWorking
            ? '#01B3E4'
            : '#FFFFFF';

        return (
            <StyledNavigation
                className           = 'send-container'
                inactivePeopleColor = {inactivePeopleColor}
                activePeopleColor   = {activePeopleColor}
                inactiveMakeColor   = {inactiveMakeColor}
                activeMakeColor     = {activeMakeColor}
            >
                <NavLink
                    to        = {`${match.url}/make`}
                    className = {`balloon balloon-make`}
                >
                    <MakeBalloonWithProgress
                        videoState       = {videoState}
                        demoStepComplete = {demoStepComplete}
                        history          = {history}
                    />
                </NavLink>

                <NavLink
                    to        = {`${match.url}/people`}
                    className = {`balloon balloon-people`}
                >
                    <PeopleBalloonWithProgress
                        peopleStepComplete={peopleStepComplete}
                    />
                </NavLink>

                <NavLink
                    to        = {`${match.url}/sendnot`}
                    className = {`balloon balloon-sendnot`}
                >
                    <SendNot />
                </NavLink>
                <div className='progress-bar' />
            </StyledNavigation>
        );
    }
}

const StyledNavigation = styled.div`
    height: 92px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    pointer-events: none;
    transition: all 0.5s 0.01s ease-in-out;
    position: relative;

    .balloon {
        transition: all 0.5s 0.01s ease;
        position: relative;
        z-index: 2147483646;

        &.active.clickable {
            pointer-events: none;
        }

        &.clickable {
            pointer-events: auto;
        }
    }

    #shapeToFill {
        transition: all 0.5s 0.01s ease-in-out;
    }

    .balloon-people {
        #shapeToFill {
            fill: ${props => props.inactivePeopleColor};
        }

        &.active {
            #shapeToFill {
                fill: ${props => props.activePeopleColor};
            }
        }
    }

    .balloon-make {
        #shapeToFill {
            fill: ${props => props.inactiveMakeColor};
        }

        &.active {
            #shapeToFill {
                fill: ${props => props.activeMakeColor};
            }
        }
    }

    .balloon-sendnot {
        #shapeToFill {
            fill: #BABABA;
        }

        &.active {
            #shapeToFill {
                fill: #01B3E4;
            }
        }
    }

    .progress-bar {
        width: 240px;
        position: absolute;
        top: 44px;
        border: 1px solid gray;
        opacity: 0.12;
        z-index: 2147483645;
    }
`;



