import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function Launching({ video }) {
    return (
        <ItIsHappening
            className    = 'launching'
            normalText   = 'screen recorder...'
            boldText     = 'Launching '
            animate      = { true }
            videoState   = { video.state }
        />
    );
}



