import React from 'react';
import { connect } from 'react-redux';
import styled, { keyframes } from 'styled-components';


export function ProgressRing({
    size         = '40',
    thickness    = '1',
    positionTop  = '0',
    positionLeft = '0',
    percent      = '50',
}) {
    let radius          = (size / 2) - thickness;
    let maxCircleLength = Math.PI * 2 * radius;
    let circleLength    = (percent * maxCircleLength) / 100;

    const StyledSvg = styled.svg`
        transform-origin: center;
        transform:        rotate(-90deg);
        position:         absolute;
        fill:             transparent;
        stroke:           #01B3E4;
        transition:       all 0.2s ease;
        stroke-width:     ${ thickness };
        top:              ${ positionTop  + 'px' };
        left:             ${ positionLeft + 'px' };
        stroke-dasharray: ${ circleLength + ' 5000' };
    `;

    if (percent == 0) {
        return null;
    }

    return (
        <StyledSvg
            className     = 'progress-ring'
            circleLength  = { circleLength }
            thickness     = { thickness }
            width         = { size }
            height        = { size }
            viewBox       = { `0 0 ${size} ${size}` }
            positionTop   = { positionTop }
            positionLeft  = { positionLeft }
        >
            <circle className='svg-circle' cx={size / 2} cy={size / 2} r={radius - 1}/>
        </StyledSvg>
    );
}

// to support multiple videos in the future:
function averageVideosProgress(videos) {
    if ( ! videos || ! Object.keys(videos)) {
        return 0;
    }

    const keys = Object.keys(videos);

    if (keys.every(videoKey => videos[videoKey].state == 'noVideo')) {
        return 0;
    }

    return (keys.reduce((acc, key) => acc + videos[key].progress, 0)) / keys.length;
}

function mapStateToProps(state) {
    return {
        percent: averageVideosProgress(state.videos) * 100
    };
}

export default connect(mapStateToProps)(ProgressRing);



