import React, { Component } from 'react';


export default function Record({ size }) {
    return (
        <svg width={size} height={size} viewBox="0 0 40 40">
            <title>Record</title>
            <g fill="none" transform="matrix(1.257 0 0 1.257 3.661 3.661)">
                <g>
                    <circle strokeWidth=".7956" stroke="#ffb26a" cy="13" cx="13" r="13" fill="#ffffff" />
                    <circle cy="13" cx="13" r="5" fill="#ff2500" />
                </g>
            </g>
        </svg>
    );
}



