import React from 'react';
import { connect } from 'react-redux';
import { Field } from 'redux-form';
import UploadArea from '../upload_area/UploadArea';


const uploadRootImages = 'static/files/';

export function UploadGroup({ change, environment, onUploaded, onUploadError }) {
    return (
        <div className='upload-group'>
            <div className='field-label'>Demo Logo or Image</div>
            <Field
                name          = 'images.logo'
                onUploaded    = { onUploaded }
                onUploadError = { onUploadError }
                component     = { UploadArea }
                change        = { change }
                environment   = { environment }
                uploadRoot    = { uploadRootImages }
                accept        = 'image/jpg,image/png,image/gif,image/bmp,image/jpeg'
            />

            <div className='field-label'>Favicon</div>
            <Field
                name          = 'images.favicon'
                onUploaded    = { onUploaded }
                onUploadError = { onUploadError }
                component     = { UploadArea }
                change        = { change }
                environment   = { environment }
                uploadRoot    = { uploadRootImages }
                accept        = 'image/png,image/gif,image/ico'
            />
        </div>
    );
}

export default connect(state => ({ environment: state.environment }))(UploadGroup);


