import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Route } from 'react-router-dom';
import AppContainer from '../app_container/AppContainer';
import LoginContainer from '../login_container/LoginContainer';
import HidePopupButton from '../hide_popup_button/HidePopupButton';


// this either shows the app or kicks user to login screen
export class PrivateContainer extends Component {
    componentWillMount() {
        this.props.dispatch({
            type: 'USER/REFRESH'
        });
    }

    render() {
        const { props } = this;
        const render    = () => (
            props.authedUser
                ? <AppContainer {...props} />
                : <LoginContainer {...props} />
        );

        return (
            <div>
                <HidePopupButton />
                <Route path={props.path} render={render} />
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        authedUser: state.authedUser,
        context:    state.context
    };
}

export default connect(mapStateToProps)(PrivateContainer);



