import React from 'react';
import { connect } from 'react-redux';
import { Field, formValueSelector } from 'redux-form';
import Checkbox from '../checkbox/Checkbox';
import TextInput from '../text_input/TextInput';


export function AnalyticsGroup({ adobeEnabled }) {
    return (
        <div className='analytics-group'>
            <div className='field-label'>Web Analytics</div>

            <Field name='analytics.vendor' component='select'>
                <option value='1'>Google Analytics</option>
                <option value='2'>Clicky</option>
                <option value='3'>Bizible</option>
                <option value='4'>Adobe Analytics</option>
            </Field><br/><br/>

            <Field
                name        = 'analytics.code'
                placeholder = 'Web Property ID'
                component   = {TextInput}
            />
            {
                adobeEnabled &&
                <Checkbox
                    name  = 'analytics.adobeConstraint'
                    label = 'Add unique constraint "26767" to s.prop13 field to avoid form id collisions'
                />
            }
        </div>
    );
}

const selector = formValueSelector('settingsForm');

function mapStateToProps (state) {
    const adobeEnabled = selector(state, 'analytics.vendor') === '4';
    return {
        adobeEnabled
    };
}

export default connect(mapStateToProps)(AnalyticsGroup);



