import React from 'react';
import styled from 'styled-components';


export default function ActionButton({ clickHandler, Icon, disabled, text }) {
    return (
        <Styled onClick={clickHandler} disabled={disabled}>
            <Icon />
            <span>{text}</span>
        </Styled>
    );
}

const Styled = styled.div`
    color: #8C8C8C;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    pointer-events: ${props => props.disabled ? 'none' : 'auto'};
    height: 50px;

    span {
        color: #C1C1C1;
        text-align: center;
        white-space: nowrap;
        margin-top: 3px;
    }

    &:hover {
        cursor: pointer;

        span {
            color: #01B3E4;
        }
    }
`;



