import React from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm, getFormSyncErrors } from 'redux-form';
import styled from 'styled-components';
import validate, { areThereErrors } from './validate';
import { hammerIn, hammerOut } from '../../helpers/hammerSettings';

import LanguageGroup from './LanguageGroup';
import ContactInfoGroup from './ContactInfoGroup';
import UploadGroup from './UploadGroup';
import ColorsGroup from './ColorsGroup';
import ThemeGroup from './ThemeGroup';
import LeadGateGroup from './LeadGateGroup';
import ShareGateGroup from './ShareGateGroup';
import CallToActionGroup from './CallToActionGroup';
import BuyerBoardGroup from './BuyerBoardGroup';
import AnalyticsGroup from './AnalyticsGroup';
import AdwordsGroup from './AdwordsGroup';
import Flash from '../flash/Flash';


export function SettingsForm({ history, handleSubmit, pristine, reset, submitting, dispatch, errors, change, latestLocation }) {
    const submit = values => {
        dispatch({
            type: 'SETTINGS/REFRESH',
            payload: hammerOut(values)
        });

        history.push(latestLocation);
    };

    const uploaded = url => {
        dispatch({
            type: 'UPLOADED/IMAGE',
            payload: url
        });
    };

    const uploadError = error => {
        // TODO: consume the error message properly
        console.error(error);
    };

    return (
        <StyledSettingsForm className='settings-form' onSubmit={handleSubmit(submit)}>
            <button type='submit' disabled={submitting}>SAVE</button>
            {
                errors &&
                <Flash type='error' message='Please fill in all the required fields.'></Flash>
            }

            <div className='groups'>
                <LanguageGroup /><br/><br/>
                <ContactInfoGroup /><br/><br/>
                <UploadGroup
                    change        = {change}
                    onUploaded    = {uploaded}
                    onUploadError = {uploadError}
                /><br/><br/>
                <CallToActionGroup /><br/><br/>
                <ColorsGroup /><br/><br/>
                <ThemeGroup
                    change        = {change}
                    onUploaded    = {uploaded}
                    onUploadError = {uploadError}
                /><br/><br/>
                <LeadGateGroup /><br/><br/>
                <ShareGateGroup /><br/><br/>
                <BuyerBoardGroup /><br/><br/>
                <AnalyticsGroup /><br/><br/>
                <AdwordsGroup />
            </div>
        </StyledSettingsForm>
    );
}

const selector = getFormSyncErrors('settingsForm');

function mapStateToProps (state) {
    const { settings, latestLocation }  = state;
    const errors        = areThereErrors(selector, state);
    const initialValues = hammerIn(settings);

    return {
        errors,
        initialValues,
        latestLocation,
    };
}

const ConnectedForm = reduxForm({
    form: 'settingsForm',
    validate,
})(SettingsForm);

export default connect(
    mapStateToProps
)(ConnectedForm);

const StyledSettingsForm = styled.form`
    position: relative;
    padding: 24px;

    .field-label {
        font-weight: bold;
        color: #6A6A6A;
        margin: 20px 0 10px 0;

        .faint {
            color: #B5B5B5;
            font-weight: normal;
        }
    }

    button[type='submit'] {
        border: none;
        background-color: white;
        color: #01B3E4;
        position: absolute;
        top: -28px;
        right: 12%;
        z-index: 1500;
    }

    select {
        width: 100%;
        height: 45px;
        background-color: white;
        color: #767676;
        border: 1px solid #ECECEC;
        border-radius: 1px;
    }
`;



