import React from 'react';
import { connect } from 'react-redux';
import styled, { keyframes } from 'styled-components';
import Recordings from '../svg_react/content_studio/Recordings';
import RecordCircle from '../svg_react/content_studio/RecordCircle';
import { StyledContentStudioButton } from './StyledContentStudioButton';


export default class RecorderButton extends React.Component {
    constructor(props) {
        super(props);
        this.clickHandler = this.clickHandler.bind(this);
    }

    clickHandler(e) {
        const {
            manage, options,
            onCancelled, onLaunching, onStarted
        } = this.props;

        let newOptions = {
            ...options,
            callback: (result) => {
                console.debug('SOM', result);

                if (result.protocolUserCancelled) {
                    onCancelled();
                }
                else {
                    onStarted({
                        protocol: result.protocolInstalled,
                        success: result.type === 'success'
                    });
                }
            }
        };

        // launch either the recorder or the manager
        newOptions.properties.showManager = manage;

        onLaunching();
        SOMLauncher.launch(newOptions);
    }

    render() {
        if ( ! this.props.options) {
            return null;
        }

        const { manage, title } = this.props;
        const id = manage
            ? 'som_recordings_button_content_studio'
            : 'som_record_button_content_studio';

        return (
            <StyledContentStudioButton id={id} onClick={this.clickHandler}>
                {
                    ! manage && <RecordCircle size='27' />
                }
                {
                    manage && <Recordings size='27' />
                }
                { title }
            </StyledContentStudioButton>
        );
    }
}



