import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function NeedDownload({ video, cancel }) {
    function downloadRecorder() {
        var ext = '.dmg';
        if (navigator.userAgent.toLowerCase().indexOf('windows') > 0) {
            ext = '.exe';
        }
        window.location = 'https://cdn.goconsensus.com/sco/20170420/InstallScreenRecorderLauncher-2.8' + ext;
        cancel();
    }

    return (
        <ItIsHappening
            className    = 'download-recorder'
            normalText   = 'Recorder not installed'
            actionText   = 'Download Recorder'
            videoState   = { video.state }
            clickHandler = { downloadRecorder }
        />
    );
}



