import React from 'react';
import { Field } from 'redux-form';
import styled from 'styled-components';
import Tick from '../svg_react/tick/Tick';


export default function Checkbox({ name, label }) {
    return (
        <StyledCheckbox className='styled-checkbox'>
            <label>
                <Field name={name} component='input' type='checkbox' />
                <div className='styled-checkbox'><Tick fill='white' /></div>
                <span>{label}</span>
            </label>
        </StyledCheckbox>
    );
}

const StyledCheckbox = styled.div`
    position: relative;
    padding: 6px 0 6px 0;

    &:hover label, input {
        cursor: pointer;
    }

    label {
        display: flex;
        align-items: center;
        color: #7F7F7F;

        div {
            width: 14px;
            height: 14px;
            border: 1px solid #BEBEBE;
            background-color: #F6F6F6;
            border-radius: 2px;
            transition: all 0.3s 0.01s ease;

            svg {
                opacity: 0;
            }
        }

        input {
            position: absolute;
            opacity: 0;
        }

        input[value='true'] ~ div {
            background-color: #01B3E4;
            border: 1px solid #01B3E4;
            transition: all 0.3s 0.01s ease;

            svg {
                opacity: 1;
            }
        }

        span {
            padding: 0 0 0 14px;
        }
    }
`;



