import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import CloseIcon from '../svg_react/close_icon/CloseIcon';


export class HidePopupButton extends Component {
    hide() {
        this.props.dispatch({
            type: 'POPUP/HIDE'
        });
    }

    render() {
        const StyledButton = styled.div`
            position: relative;
            svg {
                position: absolute;
                z-index: 100001;
                top: 19px;
                left: 12px;
            }
        `;

        return (
            <StyledButton>
                <CloseIcon clickHandler={this.hide.bind(this)} />
            </StyledButton>
        );
    }
}

export default connect(() => ({}))(HidePopupButton);



