import React from 'react';
import styled, { keyframes } from 'styled-components';

export default function TextInput({  compId, className, input, type, placeholder, defaultValue, updateHandler, name, meta: { touched, error, warning } }) {
    return (
        <StyledTextInput className={`text-input ${className}`}>
            <input
                {...input}
                id           = {compId}
                onBlur       = {updateHandler}
                placeholder  = {placeholder}
                defaultValue = {defaultValue}
                name         = {name}
                type         = {type}
                autoComplete = 'off'
            />
            { touched && (error && <span className='error'>{error}</span>) }
        </StyledTextInput>
    );
}

const kf = keyframes`
    to {
        opacity: 1;
    }
`;

const StyledTextInput = styled.div`
    width: 100%;
    position: relative;

    input {
        color: #767676;
        height: 45px;
        width: 100%;
        box-sizing: border-box;
        padding-left: 8px;
        border: 1px solid #ECECEC;

        &:focus {
            outline: 0;
        }

        &::placeholder {
            color: #A8A9A9;
        }
    }

    .error {
        opacity: 0;
        animation: ${kf} 1.2s 0.05s forwards ease;
        position: absolute;
        top: 5px;
        right: 8px;
        font-size: 9px;
        color: #FC3C1D;
    }
`;



