import React from 'react';
import styled from 'styled-components';
import AlertIcon from '../svg_react/alert_icon/AlertIcon';


export default function WarningMessage(props) {
    return (
        <Styled>
            <div className='flash-message'>
                <div className='icon'><AlertIcon size='15' /></div>
                <div className='text'>{props.children}</div>
            </div>
        </Styled>
    );
}

const Styled = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px;

    .flash-message {
        background-color: #F2DEDE;
        border: 1px solid #EBCED2;
        padding: 8px;
        display: flex;
        align-items: center;

        .icon {
            padding: 0 8px 0 0;
        }

        .text {
            color: #B35A57;
            font-size: 11px;
            line-height: 16px;
        }
    }
`;



