import React, { Component } from 'react';
import { NavLink, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { validate } from '../../helpers/validateRecipients';
import { setLatestLocation } from '../../helpers/location';
import styled from 'styled-components';
import MakeDemoContainer from '../make_demo_container/MakeDemoContainer';
import PeopleContainer from '../people_container/PeopleContainer';
import SendNoSendContainer from '../send_no_send_container/SendNoSendContainer';
import SendDemoBubbles from './SendDemoBubbles';
import SendDemoArrows from './SendDemoArrows';

import emailRequired from '../../helpers/emailRequired';


export class SendDemoContainer extends Component {
    constructor(props) {
        super(props);
        this.generateSendgroup   = this.generateSendgroup.bind(this);
        this.addBatchToSendgroup = this.addBatchToSendgroup.bind(this);
        this.resetExtension      = this.resetExtension.bind(this);
        this.cancelExtension     = this.cancelExtension.bind(this);
    }

    generateSendgroup() {
        this.props.dispatch({
            type: 'SENDGROUP/CREATE_SENDGROUP'
        });

        const newLocation = '/src/action/popup/popup.html/main/send/people';

        this.props.history.push(newLocation);
        setLatestLocation(this.props.dispatch, newLocation);
    }

    addBatchToSendgroup() {
        let validated = validate(this.props.recipientsList);

        this.props.dispatch({
            type: 'RECIPIENTS/BATCH_TO_SENDGROUP',
            payload: validated
        });

        if (validated.passed == true) {
            const newLocation = '/src/action/popup/popup.html/main/send/sendnot';

            this.props.history.push(newLocation);
            setLatestLocation(this.props.dispatch, newLocation);
        }
    }

    resetExtension() {
        window.localStorage.removeItem('JID_loose_content_group_id');
        this.props.dispatch({ type: 'EXTENSION/RESET' });

        const newLocation = '/src/action/popup/popup.html/main/send/make/rapid';

        this.props.history.push(newLocation);
        setLatestLocation(this.props.dispatch, newLocation);
    }

    cancelExtension() {
        window.localStorage.removeItem('JID_loose_content_group_id');
        this.props.dispatch({ type: 'EXTENSION/CANCEL' });

        const newLocation = '/src/action/popup/popup.html/main/send/make/rapid';

        this.props.history.push(newLocation);
        setLatestLocation(this.props.dispatch, newLocation);
    }

    render() {
        const { match, demoStepComplete, peopleStepComplete, dispatch, history, videoState } = this.props;
        const that = this;

        function MakeArrows({}) {
            let continueHandler = demoStepComplete
                ? that.generateSendgroup
                : null;

            let backHandler     = null;

            return (
                <SendDemoArrows
                    continueHandler = {continueHandler}
                    resetHandler    = {that.cancelExtension} 
                    backHandler     = {backHandler}
                    showForward     = {true}
                    showBack        = {false}
                    resetText       = 'Cancel'
                />
            );
        }

        function PeopleArrows({}) {
            let continueHandler = peopleStepComplete
                ? that.addBatchToSendgroup
                : null;

            let backHandler = () => {
                let newLocation = '/src/action/popup/popup.html/main/send/make';

                history.push(newLocation);
                setLatestLocation(dispatch, newLocation);
            };

            return (
                <SendDemoArrows
                    continueHandler = {continueHandler}
                    resetHandler    = {that.cancelExtension}
                    backHandler     = {backHandler}
                    showForward     = {true}
                    showBack        = {true}
                    resetText       = 'Cancel'
                />
            );
        }

        function NoSendArrows({}) {
            let continueHandler = null;

            let backHandler = () => {
                let newLocation = '/src/action/popup/popup.html/main/send/people';

                history.push(newLocation);
                setLatestLocation(dispatch, newLocation);
            };

            return (
                <SendDemoArrows
                    continueHandler = {continueHandler}
                    resetHandler    = {that.resetExtension}
                    backHandler     = {backHandler}
                    showForward     = {false}
                    showBack        = {true}
                    resetText       = 'New Demo'
                />
            );
        }

        const allowNavToPeople = demoStepComplete
            ? true
            : false;

        const allowNavToSend = demoStepComplete && peopleStepComplete
            ? true
            : false;

        return (
            <StyledContainer className='send-container'>
                <SendDemoBubbles
                    match              = {match}
                    videoState         = {videoState}
                    demoStepComplete   = {demoStepComplete}
                    peopleStepComplete = {peopleStepComplete}
                    allowNavToPeople   = {allowNavToPeople}
                    allowNavToSend     = {allowNavToSend}
                    history            = {history}
                />

                <div className='steps'>
                    <Route path={`${match.url}/make`}    component={MakeDemoContainer}></Route>
                    <Route path={`${match.url}/people`}  component={PeopleContainer}></Route>
                    <Route path={`${match.url}/sendnot`} component={SendNoSendContainer}></Route>
                </div>

                <div className='arrows'>
                    <Route path={`${match.url}/make`}    component={MakeArrows}></Route>
                    <Route path={`${match.url}/people`}  component={PeopleArrows}></Route>
                    <Route path={`${match.url}/sendnot`} component={NoSendArrows}></Route>
                </div>
            </StyledContainer>
        );
    }
}


const StyledContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    .arrows {
        width: 100%;
    }
`;

function mapStateToProps (state) {
    const { demo, videos, recipients, viewerPrivacy } = state;

    let emailSatisified = (emailField) =>{
        return emailRequired(viewerPrivacy)
            ? emailField !== ''
            : true;
    }

    const recipientsList = recipients.recipientList;
    let videoState       = 'videoDetails';

    Object.keys(videos).forEach(videoKey => {
        let video = videos[videoKey];

        switch (video.state) {
            case 'uploading':
                videoState = video.state;
            break;
            case 'processing':
                if (videoState !== 'uploading') {
                    videoState = video.state;
                }
            break;
            case 'noVideo':
                if (videoState !== 'uploading' && videoState !== 'processing') {
                    videoState = video.state;
                }
            break;
        }
    });

    const demoStepComplete =
        demo.current.id &&
        demo.current.title &&
        (demo.rapid.hasContent || ! demo.current.isRapid);

    const peopleStepComplete = recipientsList.length > 0
        && recipientsList.every(
            rec => rec.fname !== ''
                && rec.lname !== ''
                && emailSatisified(rec.email))
        && recipients.organization;

    return {
        demoStepComplete,
        peopleStepComplete,
        recipientsList,
        videoState,
    };
};

export default connect(mapStateToProps)(SendDemoContainer);



