import React from 'react';
import styled, { keyframes } from 'styled-components';
import UploadCloud from '../svg_react/content_studio/UploadCloud';
import ProgressRing from '../progress_ring/ProgressRing';


export default class ItIsHappening extends React.Component {
    shouldComponentUpdate(nextProps) {
        return this.props.videoState !== nextProps.videoState;
    }

    render() {
        const { boldText, normalText, actionText, clickHandler, className } = this.props;

        const StyledButton = styled.div`
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            height: 180px;

            .content {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                height: 100px;
                
                .bold {
                    font-weight: bold;
                }

                .action {
                    color: #01B3E4;
                    font-weight: bold;


                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        `;

        return (
            <StyledButton className={`it-is-happening ${className}`}>
                <div className='content'>
                    <div style={{ position: 'relative' }}>
                        <UploadCloud size='40' />
                        <ProgressRing
                            size         = '44'
                            positionTop  = '-2'
                            positionLeft = '-2'
                        />
                    </div>
                    <div>
                        <span className='bold'>{boldText}</span>
                        <span className='normal'>{normalText}</span>
                    </div>
                    <div onClick={clickHandler} className='action'>{actionText}</div>
                </div>
            </StyledButton>
        );
    }
}



