import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Route, NavLink } from 'react-router-dom';
import styled from 'styled-components';
import RapidDemoContainer from '../rapid_demo_container/RapidDemoContainer';
import ExistingDemosContainer from '../existing_demos_container/ExistingDemosContainer';


export class RecordDemoContainer extends Component {
    componentWillMount() {
        const { isRapid } = this.props;
        isRapid
            ? this.props.history.push('/src/action/popup/popup.html/main/send/make/rapid')
            : this.props.history.push('/src/action/popup/popup.html/main/send/make/existing');
    }

    render() {
        const { match } = this.props;

        return (
            <StyledContainer className='make-demo-container'>
                <div className='nav-make'>
                    <NavLink to={`${match.url}/rapid`} className='make-tab'>RAPID DEMO</NavLink>
                    <NavLink to={`${match.url}/existing` } className='make-tab'>EXISTING DEMOS</NavLink>
                </div>

                <div className='content-make'>
                    <Route path={`${match.url}/rapid`} component={RapidDemoContainer} />
                    <Route path={`${match.url}/existing`} component={ExistingDemosContainer} />
                </div>
            </StyledContainer>
        );
    }
}

function mapStateToProps(state) {
    return {
        isRapid: state.demo.current.isRapid,
    };
}

export default connect(mapStateToProps)(RecordDemoContainer);

const StyledContainer = styled.div`
    background-color: white;
    height: 360px;
    width: 360px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px;

    .nav-make {
        height: 48px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #F3F3F3;

        .make-tab {
            outline: none;
            text-decoration: none;
            color: #8C8C8C;
            padding: 12px 12px 8px 20px;
            transition: all 0.3s 0.01s ease;

            &.active {
                color: #01B3E4;
                font-weight: bold;
                transition: all 0.3s 0.01s ease;
            }
        }
    }
`;



