export function validate(recipients) {
    let validation = {
        passed: false
    };

    let marked = recipients.map(function(recipient) {
        let failCondition = recipient.email !== '' && ! validEmail(recipient.email);
        recipient.failed = failCondition;
        return recipient;
    });

    let hasFailure = undefined != marked.find(function(recipient){ return recipient.failed == true });
   
    if( hasFailure) {
        validation.recipients = marked;
    } else {
        validation.passed = true;
    }

    return validation;
}

function validEmail(email) {
    let regexPattern = /^\w+([\.\+-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,10})+$/;
    return regexPattern.test(email);
}



