import React from 'react';


export default function Tick({ fill, opacity }) {
    return (
        <svg xmlns="http://www.w3.org/2000/svg" shapeRendering="crispEdges" version="1.1" viewBox="0 0 24 24">
            <g transform="translate(0 -1028)">
                <path d="m9.188 1049 14.06-14-2.34-3-11.72 12-6.096-6-2.344 2z" fillOpacity="1" fillRule="evenodd" fill={fill} />
            </g>
        </svg>
    );
}



