import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function VideoDetails({ video, cancel }) {
    return (
        <ItIsHappening
            className    = 'video-details'
            normalText   = { video.title }
            boldText     = 'Video title: '
            actionText   = 'Delete'
            clickHandler = { cancel }
            videoState   = { video.state }
        />
    );
}



