import React from 'react';
import { connect } from 'react-redux';
import { Field, formValueSelector } from 'redux-form';
import Checkbox from '../checkbox/Checkbox';
import TextInput from '../text_input/TextInput';


export default function AdwordsGroup({ }) {
    return (
        <div className='adwords-group'>
            <div className='field-label'>Google Adwords</div>
            <Field
                name        = 'adwords.id'
                placeholder = 'Conversion ID'
                component   = {TextInput}
            /><br/>
            <Field
                name        = 'adwords.label'
                placeholder = 'Conversion Label'
                component   = {TextInput}
            />
        </div>
    );
}



