import React from 'react';
import { Route } from 'react-router-dom';
import styled from 'styled-components';
import ThreeDotMenu from '../three_dot_menu/ThreeDotMenu';


export default function Header({ match }) {
    // this will come from redux store
    var { fullName, accountType } = {
        fullName: 'Steve Jones',
        accountType: 'Account Owner'
    };

    return (
        <StyledHeader>
            <div className='left'>
                <div className='image-container'>
                    <img src='https://cdn01.demochimp.com/static/files/consensus_horizontal_logo_large.png' />
                </div>
            </div>

            <div className='right'>
                {/*<div className='account'>
                    <div className='user'>{ fullName }</div>
                    <div className='type'>{ accountType }</div>
                </div>*/}
                <div className='threedots'>
                     <Route path={`${match.url}`} component={ ThreeDotMenu } />
                </div>
            </div>
        </StyledHeader>
    );
}

const StyledHeader = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #FFFFFF;
    color: #6F6F6F;
    padding: 14px 0 16px 150px;
    font-size: 12px;
    border-bottom: 1px solid #E5E5E5;

    .left {
        .image-container {
            width: 118px;

            img {
                width: 100%;
                height: auto;
            }
        }
    }
    .right {
        display: flex;

        .account {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-between;
            padding-right: 4px;

            .user {
                font-weight: bold;
            }
        }
        .threedots {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
`;



