import styled from 'styled-components';


export default styled.input`
    color: #767676;
    height: 35px;
    width: 75%;
    box-sizing: border-box;
    padding-left: 8px;
    border: 1px solid #ECECEC;

    &:focus {
        outline: 0;
    }

    &::placeholder {
        color: #A8A9A9;
    }
`;



