import React from 'react';


export default function Cancel({ size }) {
    return (
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1" height={size} width={size} viewBox="0 0 40 40">
            <title>Cancel</title>
            <defs>
                <circle id="a" cy="18" cx="18" r="18" />
            </defs>
            <g fill="none" transform="translate(1.947 2)">
                <g transform="translate(-309,-302)">
                    <use fillRule="evenodd" transform="translate(309,302)" height="100%" width="100%" y="0" x="0" fill="#ffffff" />
                    <circle stroke="#efefef" cy="320" cx="327" r="17.5" />
                    <path d="m318 328.9 18.24-17.82" fillRule="evenodd" stroke="#afafaf" strokeWidth="2" fill="#000000" />
                    <path d="m318.3 310.5 17.76 18.55" stroke="#afafaf" strokeWidth="2" fill="none" />
                </g>
            </g>
        </svg>
    );
}



