import React from 'react';
import styled from 'styled-components';

import RecipientInput from '../recipient_input/RecipientInput';
import Person from '../svg_react/person/Person';
import CloseIcon from '../svg_react/close_icon/CloseIcon';
import Flash from '../flash/Flash';

export default function Recipient(props){
    let orange           = '#ff8b1e';
    let personHoverClass = props.count < 2
        ? 'name-line static'
        : 'name-line icon-switcher';

    return(
        <StyledPerson> {/* make person its own component */}
           <div className={personHoverClass} >
                <Person fill = { orange }/>
                <CloseIcon clickHandler = { props.deleteRecipient }/>
            </div>
           <div className='name-line'>
            <input id={props.formId + '_invited'} defaultValue = {props.invitedID} type='hidden'/>

            <RecipientInput
                updateHandler   = { props.updateMethod }
                compId          = { props.formId + '_fname'}
                additionalClass = 'recipient required'
                name            = 'fname'
                placeholder     = 'First Name'
                defaultValue    = {props.fname}
                width           = '24%'
                required
            />
            <RecipientInput
                updateHandler   = { props.updateMethod }
                compId          = { props.formId + '_lname'}
                additionalClass = 'recipient required'
                name            = 'lname'
                placeholder     = 'Last Name'
                defaultValue    = {props.lname}
                width           = '24%'
                required
            />
            <RecipientInput
                updateHandler   = { props.updateMethod }
                compId          = { props.formId + '_email'}
                additionalClass = 'recipient'
                name            = 'email'
                placeholder     = 'Email'
                defaultValue    = {props.email}
                width           = '24%'
                required        = { props.emailRequired }
            />
            <RecipientInput
                updateHandler   = { props.updateMethod }
                compId          = { props.formId + '_title'}
                additionalClass = 'recipient'
                name            = 'title'
                placeholder     = 'Title'
                defaultValue    = {props.title}
                width           = '12%'
            />
          </div>
          <div className={props.hasError ? '' : 'none'}>
              <Flash
                type    = 'error'
                message = 'Invalid Email Address'
              />
          </div>
        </StyledPerson>
    );
}

const StyledPerson = styled.div`
    background: #fff;
    margin-bottom: 15px;
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    border: 1px solid #F0F0F0;
    transition: all 0.3s 0.01s ease;
    .close-icon{
        transition: all 0.3s 0.01s ease;
        display:none;
    }

    &:hover{
        box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px;
        .icon-switcher{
            .close-icon{
                display: inline-block;
            }
            .person{
                display: none;
            }
        }
    }

    .flash-message{
        padding: 5px;
        justify-content: center;
    }

    .none{
        display: none;
    }

    .name-line{
        display:inline-block;

        &.static{
            .close-icon{
                display:none;
            }
        }

        svg{
            margin-right: 5px;
            height: 33px;
            transition: all 0.3s 0.01s ease;

            &.close-icon{
                margin: 0 19.5px 0 12px;
                fill: #ff8b1e;
            }
        }
    }

`;



