import React from 'react';
import { Field } from 'redux-form';


export default function ShareGateGroup({ }) {
    return (
        <div className='sharegate-group'>
            <div className="field-label">Encourage the Viewer to Share &nbsp;
                <span className='faint'>(Sales demos only)</span>
            </div>
            <Field name='sharegate' component='select'>
                <option value='1'>Do not encourage the viewer to share</option>
                {/*<option value='2'>After the intro video</option>
                <option value='3'>After the personalization questions</option>
                <option value='4'>After x feature videos</option>*/}
                <option value='5'>At the end of the whole demo</option>
            </Field>
        </div>
    );
}



