import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Card from '../card/Card';
import clipboard from 'clipboard-js';
import Button from '../button/Button';


export class DemoUrl extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            result: '',
        };
        this.copyResult       = this.copyResult.bind(this);
        this.resetResult      = this.resetResult.bind(this);
        this.highlightUrl     = this.highlightUrl.bind(this);
    }

    copyResult() {
        // user clicks 'COPY'
        const result = this.props.link;

        this.setState({ result });
        
        clipboard.copy(result);

        setTimeout(() => this.resetResult(), 5000);
    }

    highlightUrl(event) {
        this.refs.readOnly.select();
    }

    resetResult() {
        this.setState({ result: '' });
    }

    render() {
        const { link } = this.props;

        return (
            <Card headerText='Demo URL' className='card demo-url'>
                <StyledDemoUrl>
                    <div className='row'>
                        <input
                            readOnly
                            ref       = 'readOnly'
                            type      = 'text'
                            className = 'link'
                            value     = {link}
                            onClick   = {this.highlightUrl}
                        />
                    </div>
                    <Button
                        className       = 'make-it-happen'
                        width           = '75px'
                        height          = '28px'
                        borderRadius    = '18px'
                        color           = '#909090'
                        hoverColor      = 'white'
                        hoverBackground = '#01B3E4'
                        background      = 'white'
                        border          = '1px solid #EFEFEF'
                        padding         = '5px'
                        clickHandler    = { this.copyResult }
                    >
                        { this.state.result ? 'Copied!' : 'Copy' }
                    </Button>
                </StyledDemoUrl>
            </Card>
        );
    }
}

const StyledDemoUrl = styled.div`
    .row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 0 10px 0;
    }

    button.make-it-happen {
        position: absolute;
        top: 5px;
        right: 5px;
        transition: all, 0.5s 0.01s ease-in-out;
    }
`;

function mapStateToProps(state) {
    const { groupHash }   = state.sendGroup;
    const { environment } = state;

    return {
        link: `${environment}/play/inviteportal/${groupHash}`,
    };
}

export default connect(mapStateToProps)(DemoUrl);



