import React from 'react';
import { Field } from 'redux-form';


export default function LeadGateGroup({ }) {
    return (
        <div className='leadgate-group'>
            <div className="field-label">When to Require Sign Up &nbsp;
                <span className='faint'>(Marketing demos only)</span>
            </div>
            <Field name='leadgate' component='select'>
                <option value='1'>Before the demo starts</option>
                {/*<option value='AID'>After the intro video</option>
                <option value='APE'>After the personalization questions</option>*/}
                <option value='4'>At the end of the whole demo</option>
                <option value='5'>Don't ever show the contact form automatically</option>
            </Field>
        </div>
    );
}



