import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';
import ThreeDots from '../svg_react/three_dots/ThreeDots';


export class ThreeDotMenu extends Component {
    constructor(props) {
        super(props);

        this.state      = { menuOpen: false };
        this.setState   = this.setState.bind(this);
        this.toggleMenu = this.toggleMenu.bind(this);
        this.logout     = this.logout.bind(this);
        this.logoutHard = this.logoutHard.bind(this);
    }

    componentWillMount() {
        document.addEventListener('click', event => {
            if (this.state.menuOpen) {
                this.setState({
                    menuOpen: false
                });
            }
        });
    }

    toggleMenu(event) {
        event.stopPropagation();
        event.nativeEvent.stopImmediatePropagation();
        this.setState({
            menuOpen: !this.state.menuOpen
        });
    }

    logout() {
        this.props.dispatch({
            type: 'LOGOUT'
        });
    }

    logoutHard() {
        this.props.dispatch({
            type: 'LOGOUT_HARD'
        });
    }

    render() {
        return (
            <StyledThreeDotMenu className='menu' onClick={this.toggleMenu}>
                <ThreeDots />
                <div className={`menu${this.state.menuOpen ? ' open' : ''}`}>
                    <div className='menu-item' onClick={this.logout}>Log out</div>
                    {/*<div className='menu-item' onClick={this.logoutHard}>Log out hard</div>*/}
                    <div className='menu-item' onClick={() => this.props.history.push(`${this.props.match.url}/settings`)}>Settings</div>
                </div>
            </StyledThreeDotMenu>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

export default connect(mapStateToProps)(ThreeDotMenu);

const StyledThreeDotMenu = styled.div`
    width: 33px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 4px;
    transition: all 0.25s 0.01s ease;

    #rapid-demo {
        fill: #C2C2C2;
    }

    &:hover {
        cursor: pointer;
    }

    .menu {
        opacity: 0;
        width: 98px;
        overflow: hidden;
        background-color: white;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px;
        padding: 18px;
        position: absolute;
        z-index: 2147483647;
        top: 135%;
        right: 4px;
        transition: all 0.05s 0.01s ease-in-out;
        transform: scale(0);
        transform-origin: 100% 0;
        font-size: 15px;

        &.open {
            opacity: 1;
            transition: all 0.15s 0.01s ease-in-out;
            transform: scale(1);
        }

        .menu-item {
            height: 35px;
            display: flex;
            align-items: center;

            &:hover {
                transition: all 0.5s 0.01s ease;
                background-color: #F8F8F8;
            }
        }
    }
`;



