import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function Uploading({ video, cancel }) {
    return (
        <ItIsHappening
            className    = 'uploading'
            normalText   = 'Your video is uploading...'
            actionText   = 'Cancel'
            animate      = { true }
            clickHandler = { cancel }
            videoState   = { video.state }
        />
    );
}



