import React from 'react';
import styled from 'styled-components';


export default function Arrow({ size, left, disabled }) {
    return (
        <Styled xmlns="http://www.w3.org/2000/svg" version="1.1" height={size ? size : '40'} width={size ? size : '40'} left={left} viewBox="0 0 40 40">
            <title>Arrow</title>
            <defs>
                <circle id="a" cy="18" cx="18" r="18" />
            </defs>
            <g id="arrow-fill" fill={disabled ? '#F6F6F6' : '#01B3E4'} transform="translate(2,2)">
                <g transform="translate(-922,-571)">
                    <g transform="translate(613,269)">
                        <g transform="translate(309,302)">
                            <use fillRule="evenodd" height="100%" width="100%" y="0" x="0" fill="#ffffff" />
                            <circle stroke="#efefef" cy="18" cx="18" r="17.5" />
                            <g fill={disabled ? "#afafaf" : "#F6F6F6"} transform="translate(10,10)">
                                <polygon points="0 9 12.01 9 6.505 14.51 7.919 15.92 15.84 8 7.919 0.08058 6.505 1.495 12.01 7 0 7" />
                            </g>
                        </g>
                    </g>
                </g>
            </g>
        </Styled>
    );
}

const Styled = styled.svg`
    transform: rotate(${props => props.left ? '180deg' : 0});
`;



