import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function Cancelling({ video }) {
    return (
        <ItIsHappening
            className    = 'cancelling'
            normalText   = 'Please wait...'
            animate      = { true }
            videoState   = { video.state }
        />
    );
}



