import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled, { keyframes } from 'styled-components';
import { dateDiffInDays } from '../../helpers/dates';
import ConsensusIn from '../spinners/ConsensusIn';
import DemoLine from '../demo_line/DemoLine';
import Spinner from '../spinner/SpinnerNew';
import StyledInput from '../styled/StyledInput';
import MagGlass from '../svg_react/mag_glass/MagGlass';


export class ExistingDemosContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filter: false,
            searchTerm: '',
        };
        this.toggleFilter  = this.toggleFilter.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
        this.activeFilter  = this.activeFilter.bind(this);
    }

    componentWillMount() {
        this.props.dispatch({
            type: 'DEMOS/GET',
        });

        this.props.dispatch({
            type: 'DEMO/SELECT_EXISTING',
        });
    }

    toggleFilter(by = false) {
        this.setState({ filter: by });
    }

    changeHandler(event) {
        this.setState({ searchTerm: event.target.value });
    }

    activeFilter(filter) {
        return this.state.filter === filter ? 'selected' : '';
    }

    render() {
        const { props } = this;

        const registerSelectedDemo = (id, type, title) =>{
            props.dispatch({
                type: 'DEMO/SELECT_DEMO',
                payload:{ id, type, title }
            });
        };

        return (
            <StyledContainer className = 'existing-wrapper'>
                <Spinner
                    className       = 'spinner'
                    screenName      = 'existingDemos'
                    message         = 'Loading Your Demos...'
                    Animated        = {ConsensusIn}
                    selector        = '.existing-wrapper'
                    backgroundColor = 'white'
                />
                <div className='existing-content'>
                    <div className='search'>
                        <StyledInput onChange={this.changeHandler} type='text' placeholder='Search Demos...' /><MagGlass size={20}></MagGlass>
                        <span className='fa-search'></span>
                    </div>
                    <div className = 'select-body-row'>
                        <div className= 'select-demo-left'>
                            <ul className='demo-types'>
                                <li className={this.activeFilter(false)}      onClick={() => this.toggleFilter(false)}>All</li>
                                <li className={this.activeFilter('advanced')} onClick={() => this.toggleFilter('advanced')}>Advanced</li>
                                <li className={this.activeFilter('standard')} onClick={() => this.toggleFilter('standard')}>Standard</li>
                                <li className={this.activeFilter('single')}   onClick={() => this.toggleFilter('single')}>Single</li>
                                <li className={this.activeFilter('recent')}   onClick={() => this.toggleFilter('recent')}>Recent</li>
                            </ul>
                        </div>
                        <div className = 'select-demo-right'>
                            <ul className='DemoList'>
                            {
                                props.demos
                                .filter(demo => {
                                    switch (this.state.filter) {
                                        case false:
                                            return true;
                                        case 'recent':
                                            let now      = new Date();
                                            let demoDate = new Date(demo.modified_on);
                                            let diff     = dateDiffInDays(demoDate, now);
                                            return diff < 90;
                                        default:
                                            return demo.type === this.state.filter;
                                    }
                                })
                                .filter(demo => {
                                    if (this.state.searchTerm) {
                                        let lowerCaseDemoName     = demo.title.toLowerCase();
                                        let lowerCaseSearchTerm   = this.state.searchTerm.toLowerCase();

                                        return lowerCaseDemoName.indexOf(lowerCaseSearchTerm) > -1;
                                    }
                                    return true;
                                })
                                .map((demo, i) => (
                                    <DemoLine
                                        key          = {i}
                                        selected     = {props.selectedDemo}
                                        liValue      = {demo.id}
                                        type         = {demo.type}
                                        name         = {demo.title}
                                        clickHandler = {registerSelectedDemo}
                                    />
                                ))
                            }
                            </ul>
                        </div>
                    </div>
                </div>
            </StyledContainer>
        );
    }
}

const kf = keyframes`
    75% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
`;

const StyledContainer = styled.div`
    padding: 20px;
    height: 295px;
    box-sizing: border-box;
    position: relative;

    .search {
        margin: 0 0 14px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .existing-content {
        opacity: 0;
        animation: ${kf} 0.1s 0.01s forwards ease-out;
    }

    ul {
        padding: 0 0 0 10px;
    }

    div {
        &.select-body-row {
            border: 1px solid #ECECEC;
            display: flex;
            max-height: 180px;

            .select-demo-left {
                width:30%;
                border-right: 1px solid #ECECEC;
                display:inline-block;
            }

            .select-demo-right {
                overflow-y: auto;
                overflow-x: hidden;
                width:70%;
                display:inline-block;
                ul {
                    padding: 0;
                }
            }

            ul.demo-types li {
                color: #C8C8C8;
                list-style: none;
                padding: 5px 0 5px 0;

                &:hover {
                    cursor: pointer;
                }

                &.selected {
                    font-weight: bold;
                }
            }
        }
    }
`;

const mapStateToProps = state => {
    const selectedDemo = state.demo.current.isRapid ? { id: null, type: 'standard' } : state.demo.current;

    return {
        demos: state.demos,
        selectedDemo,
    };
};

export default connect(mapStateToProps)(ExistingDemosContainer);


