import React from 'react';
import styled from 'styled-components';
import { ChromePicker } from 'react-color';


export default class ColorPicker extends React.Component {
    componentDidMount() {
        this.nv.addEventListener('dismissColorPickers', event => this.props.dismiss());
    }

    componentWillUnmount() {
        this.nv.removeEventListener('dismissColorPickers', event => this.props.dismiss());
    }

    render() {
        const { color, className, updateColor, dismiss, placement } = this.props;

        return (
            <StyledColorPicker className={className} placement={placement}>
                <ChromePicker
                    disableAlpha
                    color            = {color}
                    onChangeComplete = {updateColor}
                />
                <div className='colorpicker-save-area' ref={elem => this.nv = elem}>
                    <span className='save-button' onClick={dismiss}>Done</span>
                </div>
            </StyledColorPicker>
        );
    }
}

const StyledColorPicker = styled.div`
    position: absolute;
    top: ${props => props.placement.top};
    left: ${props => props.placement.left};
    z-index: 20000;

    .colorpicker-save-area {
        height:           35px;
        display:          flex;
        justify-content:  flex-end;
        background-color: white;
        width:            100%;
        box-shadow:       rgba(0, 0, 0, 0.3) 0px 0px 2px, rgba(0, 0, 0, 0.3) 0px 4px 8px;
        display:          flex;
        justify-content:  flex-end;
        align-items:      center;

        .save-button {
            width:           38px;
            height:          24px;
            margin:          0 4px 0 0;
            transition:      all 0.3s ease-in-out;
            display:         flex;
            justify-content: space-around;
            align-items:     center;

            &:hover {
                cursor:           pointer;
                transition:       all 0.3s ease-in-out;
                background-color: #EBF1F3;
            }
        }
    }
`;



