import React from 'react';
import styled from 'styled-components';
import Card from '../card/Card';


export default function DemoUrl({ }) {
    const StyledDemoUrl = styled.div`
        .row {
            display: flex;
            justify-content: space-between;
            align: center;
        }

        .column {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }

        .poster-image {
            width: 142px;
            height: 92px;
            background-image: url(https://goconsensus.com/app/static/img/viewtransparentflat/transparent_design.jpg)
        }
    `;

    return (
        <Card headerText='Poster Image'>
            <StyledDemoUrl>
                <div className='row'>
                    <div className='column'>
                        <input type='text' defaultValue='AUD 2600 Demo' />
                        <label><input type='checkbox' /> Display Play Icon</label>
                    </div>
                    <div className='poster-image' />
                </div>
            </StyledDemoUrl>
        </Card>
    );
}



