import React from 'react';
import styled from 'styled-components';
import RecorderButton from '../RecorderButton';
import UploadArea from '../../upload_area/UploadArea';


export default class NoVideo extends React.Component {
    render() {
        const {
            environment, uploadStart, uploadError, uploaded, video,
            cancel, launch, start, uploadPercent, uploaderRef
         } = this.props;

        return (
            <StyledContainer className='video-area'>
                <div className='file-drop-area'>
                    <UploadArea
                        accept          = 'video/*'
                        uploaderRef     = {uploaderRef}
                        environment     = {environment}
                        onUploadStart   = {uploadStart}
                        onUploadError   = {uploadError}
                        onUploaded      = {uploaded}
                        onUploadPercent = {uploadPercent}
                    />
                </div>
                <div className='recorder-controls-area'>
                    <RecorderButton
                        manage      = {false}
                        title       = 'Record New'
                        options     = {video.options}
                        onCancelled = {cancel}
                        onLaunching = {launch}
                        onStarted   = {start}
                    />

                    <RecorderButton
                        manage      = {true}
                        title       = 'Recorded'
                        options     = {video.options}
                        onCancelled = {cancel}
                        onLaunching = {launch}
                        onStarted   = {start}
                    />
                </div>
            </StyledContainer>
        );
    }
}

const StyledContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #FCFCFC;
    border: 1px dotted #E5E5E5;
    min-height: 215px;

    .file-drop-area {
        width: 100%;
        box-sizing: border-box;
        height: 80px;
        padding: 10px 0 10px 0;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-around;
    }

    .recorder-controls-area {
        width: 65%;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
    }
`;



