import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import Consensus from '../svg_react/logo/Consensus';


export default function ConsensusIn({ }) {
    return (
        <StyledLogo>
            <Consensus size='80' fill/>
        </StyledLogo>
    );
}

const kf = keyframes`
    to {
        opacity: 1;
    }
`;

const StyledLogo = styled.div`
    #twooclock {
        opacity: 0;
        animation: ${kf} 0.2s 0.5s forwards ease;
    }

    #tenoclock {
        opacity: 0;
        animation: ${kf} 0.2s 0.65s forwards ease;
    }

    #nineoclock {
        opacity: 0;
        animation: ${kf} 0.2s 0.8s forwards ease;
    }

    #eightoclock {
        opacity: 0;
        animation: ${kf} 0.2s 0.95s forwards ease;
    }

    #fouroclock {
        opacity: 0;
        animation: ${kf} 0.2s 1.1s forwards ease;
    }
`;



