import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';


export function EnvironmentSelector({ environment, dispatch }) {
    function handleChange(e) {
        dispatch({
            type: 'ENVIRONMENT/CHANGE',
            payload: e.target.value
        });
    }

    return (
        <StyledEnvForm>
            <select name='environment' value={environment} onChange={handleChange}>
                <option value='https://local.goconsensus.com/app'>local</option>
                <option value='https://auto-dr.goconsensus.com/app'>auto-dr</option>
                <option value='https://auto-af.goconsensus.com/app'>auto-af</option>
                <option value='https://drnew.goconsensus.com/app'>dr new</option>
                <option value='https://www.goconsensus.com/app'>production</option>
            </select>
        </StyledEnvForm>
    );
}

function mapStateToProps (state) {
    return {
        environment: state.environment,
    };
}

export default connect(
    mapStateToProps
)(EnvironmentSelector);

const StyledEnvForm = styled.form`
    width: 100%;
    padding-top: 10px;
`;



