import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import { connect } from 'react-redux';
import LoginForm from '../login_form/LoginForm';
import ConsensusIn from '../spinners/ConsensusIn';
import Flash from '../flash/Flash';
import Spinner from '../spinner/SpinnerNew';


export class LoginContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            windowHeight: 0,
        };
    }

    componentDidMount() {
        const windowHeight = window.innerHeight;
        this.setState({ windowHeight });
    }

    render() {
        const { flash } = this.props;

        return (
            <StyledContainer className='login-screen' windowHeight={this.state.windowHeight}>
                <div className='login-controls'>
                    <Spinner
                        screenName      = 'login'
                        message         = 'Logging you in...'
                        selector        = '.login-controls'
                        Animated        = {ConsensusIn}
                        backgroundColor = '#F8F8F8'
                    />
                    <div className='svg-container'>
                        <ConsensusIn size='70'/>
                    </div>

                    <div className='image-container'>
                        {/* TODO: find an image from cdn */}
                        <img src='https://dc-uploads-test.s3.amazonaws.com/static/files/thumbnail/consensus-gfv.png' />
                    </div>

                    { flash && <Flash type='error' message={flash} /> }

                    <div className='form-container'>
                        <LoginForm />
                    </div>
                </div>
            </StyledContainer>
        );
    }
}

function mapStateToProps(state) {
    return {
        flash: state.flash && state.flash.login,
    };
}

export default connect(mapStateToProps)(LoginContainer);

const kf = keyframes`
    to {
        opacity: 1;
    }
`;

const StyledContainer = styled.div`
    padding: 16px;
    background-color: #F6F6F6;
    height: ${props => props.windowHeight + 'px'};
    box-sizing: border-box;

    .login-controls {
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 400px;

        .svg-container {
            padding: 4px 0 12px 0;
        }

        .image-container {
            width: 135px;
            margin-bottom: 24px;
            opacity: 0;
            animation: ${kf} 0.5s 1.25s forwards ease;

            img {
                width: 100%;
                height: auto;
            }
        }

        .form-container {
            width: 100%;
            overflow: hidden;
            height: 250px;
        }
    }
`;



