import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import Consensus from '../svg_react/logo/Consensus';


export default function ConsensusSpin({ size, height, delay }) {
    const kf = keyframes`
        to {
            opacity: 1;
        }
    `;

    var delay = delay || 1;

    const StyledLogo = styled.div`
        width: 100%;
        height: ${height ? height + 'px' : 'auto'};
        display: flex;
        align-items: center;
        justify-content: space-around;

        #twooclock {
            opacity: 0;
            animation: ${kf} 2s ${delay + 's'} forwards ease;
        }

        #tenoclock {
            opacity: 0;
            animation: ${kf} 2s ${delay + 1 + 's'} forwards ease;
        }

        #nineoclock {
            opacity: 0;
            animation: ${kf} 2s ${delay + 2 + 's'} forwards ease;
        }

        #eightoclock {
            opacity: 0;
            animation: ${kf} 2s ${delay + 3 + 's'} forwards ease;
        }

        #fouroclock {
            opacity: 0;
            animation: ${kf} 2s ${delay + 4 + 's'} forwards ease;
        }
    `;

    return (
        <StyledLogo>
            <Consensus size={size} fill />
        </StyledLogo>
    );
}




