import React, { Component } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/**
 * To use the spinner have your saga dispatch:
 * { type: 'SPINNER/ON,  payload: [screenName] } to start,
 * { type: 'SPINNER/OFF, payload: [screenName] } to stop
 */
class SpinnerNew extends Component {
    constructor(props) {
        super(props);
        this.state = {
            height: 0,
            width: 0,
        };
    }

    componentDidMount() {
        let height = document.querySelector(this.props.selector).offsetHeight;
        let width  = document.querySelector(this.props.selector).offsetWidth;

        this.setState({
            height,
            width
        });
    }

    render(props) {
        // the parent needs to provide a screenName
        // prop that matches state.spinners[screenName]
        let { screenName, activeSpinners, message, Animated, backgroundColor, className } = this.props;

        // defaults so we can quickly take a spinner for a spin
        Animated = Animated || (() => <h1>Spinner</h1>);

        if (activeSpinners[screenName]) {
            return (
                <StyledSpinner
                    width           = {`${this.state.width}px`}
                    height          = {`${this.state.height}px`}
                    backgroundColor = {backgroundColor}
                    className       = {className}
                >
                    <Animated />
                    {
                        message &&
                        <span className='message'>{ message }</span>
                    }
                </StyledSpinner>
            );
        }

        return null;
    }
}

const StyledSpinner = styled.div`
    box-sizing:       border-box;
    top:              0;
    left:             0;
    position:         absolute;
    width:            ${props => props.width};
    height:           ${props => props.height};
    display:          flex;
    flex-direction:   column;
    justify-content:  space-around;
    align-items:      center;
    z-index:          100000;
    background-color: ${props => props.backgroundColor};
`;

SpinnerNew.propTypes = {
    // Animated:   PropTypes.element, TO-DO make this work
    message:         PropTypes.string,
    screenName:      PropTypes.string.isRequired,
    backgroundColor: PropTypes.string.isRequired,

    dimensions: PropTypes.shape({
        height: PropTypes.number,
        width:  PropTypes.number,
    }),

    activeSpinners: PropTypes.objectOf(PropTypes.bool)
};

function mapStateToProps(state) {
    return {
        activeSpinners: state.spinners || {}
    };
}

export default connect(mapStateToProps)(SpinnerNew);



