import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Card from '../card/Card';
import clipboard from 'clipboard-js';
import Button from '../button/Button';
import ColorPicker from '../color_picker/ColorPicker';


function PreviewColor({ backgroundColor, label, clickHandler }) {
    const StyledPreviewColor = styled.div`
        display:     flex;
        align-items: center;
        margin:      0 0 0 20px;

        .color {
            background-color: ${props => props.backgroundColor};
            border:           1px solid #B3B3B3;
            width:            20px;
            height:           20px;
            margin:           0 4px 0 0;
            border-radius:    4px;
        }

        .label {
            margin: 0 5px 0 5px;
        }
    `;

    return (
        <StyledPreviewColor onClick={clickHandler} backgroundColor={backgroundColor}>
            <div className='color'></div>
            <div className='label'>{label}</div>
        </StyledPreviewColor>
    );
}

function PreviewButton({ radius, buttonColor, text, textColor, href }) {
    let styleObj = {
        borderRadius:    radius + 'px',
        backgroundColor: buttonColor,
        color:           textColor,
        display:         'inline-block',
        fontFamily:      'inherit',
        fontSize:        '14px',
        textAlign:       'center',
        textDecoration:  'none',
        padding:         '6px 12px 6px 12px',
        border:          '1px solid #B3B3B3'
    };

    function clickHandler() {
        chrome.tabs.create({ url: href });
    }

    return (
        <a className='preview-button' style={styleObj} href={href} target='new'>{text}</a>
    );
}

export class ButtonLink extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // just local state
            result:            '',
            buttonColorPicker: false,
            textColorPicker:   false,

            // these end up in the redux store when user clicks 'Copy'
            text:        this.props.text,
            radius:      this.props.radius,
            buttonColor: this.props.buttonColor,
            textColor:   this.props.textColor,
        };
        this.updateRadius   = this.updateRadius.bind(this);
        this.updateText     = this.updateText.bind(this);
        this.updateColor    = this.updateColor.bind(this);
        this.copyResult     = this.copyResult.bind(this);
        this.resetResult    = this.resetResult.bind(this);
        this.activatePicker = this.activatePicker.bind(this);
        this.hidePicker     = this.hidePicker.bind(this);
    }

    updateRadius(event) {
        this.setState({
            radius: event.target.value
        });
    }

    updateText(event) {
        this.setState({
            text: event.target.value
        });
    }

    copyResult() {
        let result = document.querySelector('.preview-button').outerHTML;
        this.setState({ result });

        if (this.props.context === 'ext') {
            clipboard.copy({ "text/html": result, "text/plain": result });
        }
        else {
            let buttonObj = {
                type: 'button',
                html: result,
            };

            this.props.dispatch({
                type: 'GMAIL/INSERT_BUTTON',
                payload: buttonObj,
            });
        }

        this.props.dispatch({
            type: 'DEMO_BUTTON/UPDATE',
            payload: {
                text:        this.state.text,
                radius:      this.state.radius,
                buttonColor: this.state.buttonColor,
                textColor:   this.state.textColor,
            }
        });

        setTimeout(() => this.resetResult(), 3000);
    }

    activatePicker(picker) {
        return () => {
            this.setState({ [picker + 'Picker']: true, result: '' });
        };
    }

    hidePicker(picker) {
        return () => this.setState({
            [picker + 'Picker']: false,
        });
    }

    updateColor(picker) {
        return color => this.setState({
            [picker]: color.hex
        });
    }

    resetResult() {
        this.setState({ result: '' });
    }

    render() {
        let copyText   = this.props.context === 'ext'
            ? 'Copy'
            : 'Insert';

        let copiedText = this.props.context === 'ext'
            ? 'Copied!'
            : 'Inserted!';

        return (
            <Card headerText='Button Link' className='card button-link'>
                <StyledDemoUrl>
                    <div className='row'>
                        <input
                            type        = 'text'
                            placeholder = 'Visible text for your button'
                            className   = 'button-text'
                            value       = {this.state.text}
                            onChange    = {this.updateText}
                            onFocus     = {this.resetResult}
                        />
                        <PreviewButton
                            radius      = {this.state.radius}
                            buttonColor = {this.state.buttonColor}
                            text        = {this.state.text}
                            textColor   = {this.state.textColor}
                            href        = {this.props.link}
                        />
                    </div>
                    <div className='row start'>
                        <input
                            size         = '3'
                            type         = 'text'
                            className    = 'radius'
                            placeholder  = 'Enter radius'
                            value        = {this.state.radius}
                            onChange     = {this.updateRadius}
                            onFocus      = {this.resetResult}
                        /> Radius
                        <div className='color' onClick={e => e.stopPropagation()}>
                        {
                            this.state.buttonColorPicker
                            ?
                                <ColorPicker
                                    className   = 'buttonColor'
                                    color       = {this.state.buttonColor}
                                    updateColor = {this.updateColor('buttonColor')}
                                    dismiss     = {this.hidePicker('buttonColor')}
                                    placement   = {{ top: '-70px', left: '-82px' }}
                                />
                            :
                                <PreviewColor
                                    label           = 'Fill'
                                    clickHandler    = {this.activatePicker('buttonColor')}
                                    backgroundColor = {this.state.buttonColor}
                                />
                        }
                        </div>
                        <div className='color' onClick={e => e.stopPropagation()}>
                        {
                            this.state.textColorPicker
                            ?
                                <ColorPicker
                                    className   = 'textColor'
                                    color       = {this.state.textColor}
                                    updateColor = {this.updateColor('textColor')}
                                    dismiss     = {this.hidePicker('textColor')}
                                    placement   = {{ top: '-70px', left: '-152px' }}
                                />
                            :
                                <PreviewColor
                                    label           = 'Text'
                                    clickHandler    = {this.activatePicker('textColor')}
                                    backgroundColor = {this.state.textColor}
                                />
                        }
                        </div>
                    </div>
                    <Button
                        className       = 'make-it-happen'
                        width           = '75px'
                        height          = '28px'
                        borderRadius    = '18px'
                        color           = '#909090'
                        hoverColor      = 'white'
                        hoverBackground = '#01B3E4'
                        background      = 'white'
                        border          = '1px solid #EFEFEF'
                        padding         = '5px'
                        clickHandler    = {this.copyResult}
                    >
                        {this.state.result ? copiedText : copyText}
                    </Button>
                </StyledDemoUrl>
            </Card>
        );
    }
}

const StyledDemoUrl = styled.div`
    .color {
        position: relative;
    }

    .row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 0 10px 0;

        &.start {
            justify-content: flex-start;
            margin: 20px 0 10px 0;
        }
    }

    input.radius {
        height: 20px !important;
        width: 20px !important;
        margin-right: 10px;
    }

    input.button-text {
        margin-right: 10px;
    }

    .make-it-happen {
        position: absolute;
        top: 5px;
        right: 5px;
        transition: all, 0.5s 0.01s ease-in-out;
    }
`;

function mapStateToProps(state) {
    const { groupHash }   = state.sendGroup;
    const { environment } = state;

    return {
        link:        `${environment}/play/inviteportal/${groupHash}`,
        text:        state.demoButton.text,
        radius:      state.demoButton.radius,
        buttonColor: state.demoButton.buttonColor,
        textColor:   state.demoButton.textColor,
        context:     state.context,
    };
}

export default connect(mapStateToProps)(ButtonLink);



