import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function Started({ video }) {
    return (
        <ItIsHappening
            className  = 'started'
            normalText = 'Started Screen Recorder'
            videoState = { video.state }
        />
    );
}



