import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import VideoContainer from '../video_container/VideoContainer';
import ConsensusSpin from '../spinners/ConsensusSpin';
import StyledInput from '../styled/StyledInput';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/debounceTime';

export class RapidDemoContainer extends React.Component {
    constructor(props) {
        super(props);

        this.state = { rapidTitle: '' };

        this.changeRapidTitle = this.changeRapidTitle.bind(this);
        this.getValue         = this.getValue.bind(this);
    }

    shouldComponentUpdate() {
        return Object.keys(this.props.videos).length;
    }

    componentWillMount() {
        this.props.dispatch({ type: 'DEMO/SELECT_RAPID' });
        this.setState({ rapidTitle: this.props.demo.rapid.title });
    }

    componentDidMount() {
        // Becasuse we don't want to dispatch an action at every keystroke:
        const input  = document.querySelector('#demo-title-input');
        const stream = Observable.fromEvent(input, 'input');

        stream
            .debounceTime(200)
            .subscribe(() => this.props.dispatch({
                type: 'DEMO/CHANGE_TITLE',
                payload: this.state.rapidTitle,
            }));
    }

    changeRapidTitle(e) {
        this.setState({ rapidTitle: e.target.value });
    }

    getValue() {
        return this.state.rapidTitle || this.props.demo.rapid.title;
    }

    render () {
        const { videos, demo } = this.props;
        return (
            <StyledContainer className='content-studio-container'>
                <div className='demo-title'>
                    <StyledInput
                        type         = 'text'
                        placeholder  = 'Demo Title'
                        value        = { this.getValue() }
                        onChange     = { this.changeRapidTitle }
                        type         = 'text'
                        id           = 'demo-title-input'
                    />
                </div>
                {
                    ! videos || ! Object.keys(videos).length &&
                    <ConsensusSpin size='80' height='170' delay={0.01} />
                }
                {
                    Object.keys(videos).map(video => (
                        <VideoContainer key={video} video={videos[video]} />
                    ))
                }
            </StyledContainer>
        );
    }
}

function mapStateToProps(state) {
    return {
        videos: state.videos,
        demo: state.demo
    };
}

export default connect(mapStateToProps)(RapidDemoContainer);

const StyledContainer = styled.div`
    padding: 0 20px 0 20px;
    background-color: white;
    color: #8D8D8D;

    .demo-title {
        padding: 20px 0 20px 0;
        width: 100%;
    }
`;



