import React from 'react';


export default function SendNot({ }) {
    return (
        <svg width="40px" height="40px" viewBox="0 0 40 40">
            <g id="design" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                <g id="input-field" transform="translate(-921.000000, -200.000000)">
                    <g id="Group-11" transform="translate(589.000000, 80.000000)">
                        <g id="icon-wizard-send" transform="translate(332.000000, 120.000000)">
                            <circle id="shapeToFill" fill="#BABABA" cx="20" cy="20" r="20"></circle>
                            <g id="send" transform="translate(10.000000, 11.000000)" fillRule="nonzero" fill="#F6F6F6">
                                <polygon id="Shape" points="0.01 18 21 9 0.01 0 0 7 15 9 0 11"></polygon>
                            </g>
                        </g>
                    </g>
                </g>
            </g>
        </svg>
    );
}



