import React from 'react';
import styled from 'styled-components';
import ActionButton from '../action_button/ActionButton';
import Button from '../button/Button';


const StyledConfirmPopup = styled.div`
    position: absolute;
    background-color: rgba(255, 255, 255, 0.7);
    top: -5%;
    left: -315%;
    width: 250px;
    z-index: 50000;
    padding: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px;
    border: 1px solid #f2f2f2;
    color: '#7D7D7D';

    .buttons {
        display: flex;
    }
`;

function ConfirmPopup({ clickHandler, cancelHandler, text, isOpen }) {
    if (! isOpen) {
        return null;
    }

    return (
        <StyledConfirmPopup>
            {text}
            <div className='buttons'>
                <Button
                    clickHandler    = {cancelHandler}
                    background      = '#01B3E4'
                    hoverBackground = '#2DC3EC'
                    hoverColor      = 'white'
                    margin          = '16px 5px 0 0'
                >
                    No
                </Button>
                <Button
                    background      = '#CA726F'
                    hoverBackground = '#CD8380'
                    hoverColor      = 'white'
                    clickHandler    = {clickHandler}
                    margin          = '16px 0 0 5px'
                >
                    Yes
                </Button>
            </div>
        </StyledConfirmPopup>
    );
}


export default class ConfirmButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            popupOpen: false
        };

        this.confirmHandler = this.confirmHandler.bind(this);
        this.cancelHandler  = this.cancelHandler.bind(this);
        this.openPopup      = this.openPopup.bind(this);
    }

    confirmHandler(clickHandler) {
        clickHandler();
        this.cancelHandler();
    }

    cancelHandler() {
        this.setState({
            popupOpen: false,
        });
    }

    openPopup() {
        this.setState({
            popupOpen: true,
        });
    }

    render() {
        const { clickHandler, Icon, disabled, text, confirmText } = this.props;
        return (
            <StyledConfirmButton>
                <ConfirmPopup
                    isOpen        = {this.state.popupOpen}
                    clickHandler  = {() => this.confirmHandler(clickHandler)}
                    cancelHandler = {this.cancelHandler}
                    text          = {confirmText}
                />
                <ActionButton
                    clickHandler = {this.openPopup}
                    Icon         = {Icon}
                    text         = {text}
                    disabled     = {disabled}
                />
            </StyledConfirmButton>
        );
    }
}

const StyledConfirmButton = styled.div`
    position: relative;
`;



