import React from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm, formValueSelector } from 'redux-form';
import styled from 'styled-components';
import RadioButton from '../radio_button/RadioButton';
import UploadArea from '../upload_area/UploadArea';


function ActiveBox({ on, ...props }) {
    const Box = styled.div`
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid #EBEBEB;
        box-sizing: border-box;
        background-color: ${on ? '#EBEBEB' : '#F6F6F6'};
        padding: 5px 10px 10px 10px;
        border-radius: 2px;
        transition: all 3s 0.01s ease;
    `;

    return (
        <Box className='box' on={on}>
            { props.children }
        </Box>
    );
}

const uploadRootImages = 'static/files/';

export class ThemeGroup extends React.Component {
    render() {
        const {
            themeType, environment, change,
            onUploaded, onUploadError
        } = this.props;

        return (
            <StyledThemeGroup className='theme-group'>
                <div className='field-label'>Theme</div>
                <div className='themes'>
                    <ActiveBox on={themeType == 2}>
                        <RadioButton
                            name     = 'theme'
                            label    = 'Transparent'
                            value    = '2'
                        />
                        <div className='image image-transparent'/>
                        {
                            themeType == 2 &&
                            <Field
                                name          = 'images.background'
                                component     = { UploadArea }
                                onUploadError = { onUploadError }
                                onUploaded    = { onUploaded }
                                change        = { change }
                                environment   = { environment }
                                uploadRoot    = { uploadRootImages }
                                accept        = 'image/jpg,image/png,image/gif,image/bmp,image/jpeg'
                            />
                        }
                    </ActiveBox>
                    <ActiveBox on={themeType == 1}>
                        <RadioButton
                            name     = 'theme'
                            label    = 'Flat Design'
                            value    = '1'
                        />
                        <div className='image image-flat'/>
                    </ActiveBox>
                </div>
            </StyledThemeGroup>
        );
    }
}

const selector = formValueSelector('settingsForm');

const mapStateToProps = state => ({
    themeType: selector(state, 'theme'),
    environment: state.environment,
});

export default connect(mapStateToProps)(ThemeGroup);

const StyledThemeGroup = styled.div`
    .themes {
        display: flex;
        justify-content: space-around;
    }
    .image {
        margin-bottom: 5px;
        background-size: cover;
        width: 142px;
        height: 92px;

        &.image-transparent {
            background-image: url(https://goconsensus.com/app/static/img/viewtransparentflat/transparent_design.jpg)
        }
        &.image-flat {
            background-image: url(https://goconsensus.com/app/static/img/viewflat/flat_design.jpg)
        }
    }
`;



