import React from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm, formValueSelector } from 'redux-form';
import styled from 'styled-components';
import TextInput from '../text_input/TextInput';
import Checkbox from '../checkbox/Checkbox';
import RadioButton from '../radio_button/RadioButton';


function ActiveBox({ on, ...props }) {
    const Box = styled.div`
        border: 1px solid #EBEBEB;
        margin: 7px 0 0 0;
        box-sizing: border-box;
        background-color: ${props => props.on ? '#EBEBEB' : 'transparent'};
        padding: 5px 10px 5px 10px;
    `;

    return (
        <Box className='gray-box' on={on}>
            { props.children }
        </Box>
    );
}


export function CallToActionGroup({ freeTrialEnabled, buyNowEnabled, contactMeEnabled, customButtonEnabled, use }) {
    const displayFreeTrial    = !(buyNowEnabled && contactMeEnabled && customButtonEnabled);
    const displayContactMe    = !(buyNowEnabled && freeTrialEnabled && customButtonEnabled);
    const displayBuyNow       = !(contactMeEnabled && freeTrialEnabled && customButtonEnabled);
    const displayCustomButton = !(contactMeEnabled && freeTrialEnabled && buyNowEnabled);

    return (
        <StyledCallToActionGroup>
            <div className='field-label'>Call to Action</div>

            {/* 1- free trial */}
            {
                displayFreeTrial &&
                <ActiveBox className='grey-box' on={freeTrialEnabled}>
                    <Checkbox name='cta.freeTrial.enabled' label='Start Free Trial'/>
                    {
                        freeTrialEnabled &&
                        <div className='subgroup'>
                            <Field
                                name        = 'cta.freeTrial.link'
                                component   = {TextInput}
                                type        = 'text'
                                placeholder = 'Link'
                            />

                            <Checkbox name='cta.freeTrial.skipLeadGate' label='Skip Lead Capture Form'/>
                        </div>
                    }
                </ActiveBox>
            }

            {/* 2-contact me */}
            {
                displayContactMe &&
                <ActiveBox on={contactMeEnabled}>
                    <Checkbox name='cta.contactMe.enabled' label='Contact Me'/>
                </ActiveBox>
            }

            {/* 3- buy now */}
            {
                displayBuyNow &&
                <ActiveBox on={buyNowEnabled}>
                    <Checkbox name='cta.buyNow.enabled' label='Buy Now'/>
                    {
                        buyNowEnabled &&
                        <div className='subgroup'>
                            <Field
                                name        = 'cta.buyNow.link'
                                component   = {TextInput}
                                type        = 'text'
                                placeholder = 'Link'
                            />
                        </div>
                    }
                </ActiveBox>
            }

            {/* 4- custom button */}
            {
                displayCustomButton &&
                <ActiveBox on={customButtonEnabled}>
                    <Checkbox name='cta.customButton.enabled' label='Custom Button'/>
                    {
                        customButtonEnabled &&
                        <div className='subgroup'>
                            <Field
                                name        = 'cta.customButton.label'
                                component   = {TextInput}
                                type        = 'text'
                                placeholder = 'Label'
                            />
                            <RadioButton
                                name  = 'cta.customButton.use'
                                value = 'default'
                                label = 'Use default lead collection form'
                            />
                            <RadioButton
                                name  = 'cta.customButton.use'
                                value = 'external'
                                label = 'Link to external website'
                            />
                            {
                                use === 'external' &&
                                <div className='subgroup'>
                                    <Field
                                        name        = 'cta.customButton.link'
                                        component   = {TextInput}
                                        type        = 'text'
                                        placeholder = 'External Link'
                                    />
                                </div>
                            }
                        </div>
                    }
                </ActiveBox>
            }
        </StyledCallToActionGroup>
    );
}

const selector = formValueSelector('settingsForm');

const mapStateToProps = state => {
    const freeTrialEnabled    = selector(state, 'cta.freeTrial.enabled');
    const buyNowEnabled       = selector(state, 'cta.buyNow.enabled');
    const contactMeEnabled    = selector(state, 'cta.contactMe.enabled');
    const customButtonEnabled = selector(state, 'cta.customButton.enabled');
    const use                 = selector(state, 'cta.customButton.use');

    return {
        freeTrialEnabled,
        buyNowEnabled,
        contactMeEnabled,
        customButtonEnabled,
        use,
    };
};

export default connect(
    mapStateToProps
)(CallToActionGroup);

const StyledCallToActionGroup = styled.div`
    .subgroup {
        padding: 5px 0 0 0;
    }
`;



