import flatten from 'flat';


export default function validate(values) {
    if (!values || !Object.keys(values).length) return {};

    const errors = {
        contactInfo: {},

        cta: {
            buyNow: {},
            customButton: {},
            freeTrial: {},
        }
    };

    if (!values.contactInfo.phone) {
        errors.contactInfo.phone = 'Required';
    }

    if (
        values.cta.freeTrial.enabled &&
        !values.cta.freeTrial.link
    ) {
        errors.cta.freeTrial.link = 'Required';
    }

    if (
        values.cta.buyNow.enabled &&
        !values.cta.buyNow.link
    ) {
        errors.cta.buyNow.link = 'Required';
    }

    if (
        values.cta.customButton.enabled &&
        !values.cta.customButton.label
    ) {
        errors.cta.customButton.label = 'Required';
    }

    if (
        values.cta.customButton.enabled &&
        values.cta.customButton.use === 'external' &&
        !values.cta.customButton.link
    ) {
        errors.cta.customButton.link = 'Required';
    }

    return errors;
}

export function areThereErrors(selector, state) {
    const errors = selector(state);

    if (!errors) {
        return false;
    }

    const flatErrors = flatten(errors);

    for (let error in flatErrors) {
        if (typeof flatErrors[error] === 'string') {
            return true;
        }
    }

    return false;
}



