import React from 'react';
import ItIsHappening from '../ItIsHappening';


export default function PromptQuestion({ video, cancel }) {
    return (
        <ItIsHappening
            className    = 'prompt-question'
            normalText   = 'Did you see the prompt?'
            actionText   = 'Try Again'
            clickHandler = { cancel }
            video        = { video }
            videoState   = { video.state }
        />
    );
}



