import React from 'react';
import { Field } from 'redux-form';
import styled from 'styled-components';


export default function RadioButton({ name, value, label }) {
    return (
        <StyledRadio>
            <label>
                <Field name={name} value={value} component='input' type='radio' />
                <div></div>
                <span>{label}</span>
            </label>
        </StyledRadio>
    );
}

export const StyledRadio = styled.div`
    position: relative;
    padding: 6px 0 6px 0;

    &:hover label, input {
        cursor: pointer;
    }

    label {
        display: flex;
        align-items: center;
        color: #7F7F7F;

        div {
            width: 14px;
            height: 14px;
            border: 1px solid #BEBEBE;
            background-color: #F6F6F6;
            transition: all 0.3s 0.01s ease;
            border-radius: 50%;
        }

        input {
            position: absolute;
            opacity: 0;
        }

        input:checked ~ div {
            background-color: #01B3E4;
            transition: all 0.3s 0.01s ease;
            border: 1px solid #01B3E4;
        }

        span {
            padding: 0 0 0 14px;
        }
    }
`;



