import React from 'react';
import styled from 'styled-components';


export default function CloseIcon({ clickHandler }) {
    return (
        <StyledCloseIcon className='close-icon' onClick={ clickHandler } width='14px' height='14px' viewBox='0 0 14 14'>
            <g id='demo-player' stroke='none' strokeWidth='1' fillRule='evenodd'>
                <g id='toast' transform='translate(-609.000000, -82.000000)' fillRule='nonzero'>
                    <g id='close' transform='translate(609.000000, 82.000000)'>
                        <polygon id='Shape' points='14 1.41 12.59 0 7 5.59 1.41 0 0 1.41 5.59 7 0 12.59 1.41 14 7 8.41 12.59 14 14 12.59 8.41 7'></polygon>
                    </g>
                </g>
            </g>
        </StyledCloseIcon>
    );
}

const StyledCloseIcon = styled.svg`
    fill: grey;
    transition: all 0.1s 0.05s ease-in;

    &:hover {
        cursor: pointer;
        fill: darkgrey;
    }
`;



