import React, { Component } from 'react';
import styled from 'styled-components';
import ActionButton from '../action_button/ActionButton';
import Arrow from '../svg_react/arrow/Arrow';
import RestartArrow from '../svg_react/restart_arrow/RestartArrow';
import Cancel from '../svg_react/cancel/Cancel';
import ConfirmButton from '../confirm_button/ConfirmButton';


export default class SendDemoArrows extends Component {
    render() {
        const { backHandler, showBack, continueHandler, showForward, resetHandler, resetText, showReset } = this.props;

        const LeftArrow  = () => <Arrow disabled={! backHandler}     size='40' left />;
        const RightArrow = () => <Arrow disabled={! continueHandler} size='40' />;
        const CancelIcon = () => <Cancel size='25' />;

        return (
            <StyledArrows className='send-arrows'>
                <div className = 'sub-wrapper'>
                {
                    showBack &&
                    <ActionButton
                        clickHandler = {backHandler}
                        disabled     = {! backHandler}
                        Icon         = {LeftArrow}
                        text         = 'Back'
                    />
                }
                </div>

                <div className = 'sub-wrapper'>
                {
                    resetText == 'New Demo'
                    ?
                        <ActionButton
                            clickHandler = {resetHandler}
                            Icon         = {RestartArrow}
                            text         = {resetText}
                            disabled     = {false}
                        />
                    :
                        <ConfirmButton
                            confirmText  = 'Are you sure you want to cancel this demo and start a new demo? All progress and any video files will be deleted.'
                            clickHandler = {resetHandler}
                            Icon         = {CancelIcon}
                            text         = {resetText}
                            disabled     = {false}
                        />
                }
                </div>

                <div className = 'sub-wrapper'>
                {
                    showForward &&
                    <ActionButton
                        clickHandler = {continueHandler}
                        Icon         = {RightArrow}
                        disabled     = {! continueHandler}
                        text         = 'Continue'
                    />
                }
                </div>
            </StyledArrows>
        );
    }
}

const StyledArrows = styled.div`
    display: flex;
    justify-content: space-around;
    width: 100%;
    padding: 30px 0 30px 0;

    .sub-wrapper {
        height: 40px;
        width: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
`;



