import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm, formValueSelector } from 'redux-form';
import styled from 'styled-components';
import { ChromePicker } from 'react-color';


function ColorPickerConnected ({ input, ...rest }) {
    return (
        <ChromePicker
            disableAlpha = {true}
            color        = {input.value}
            {...input}
            {...rest}
        />
    );
}

function ReadOnly({ hex, clickHandler }) {
    const StyledReadOnly = styled.div`
        display: flex;
        height: 45px;
        width: 100%;
        color: #7F7F7F;

        &:hover {
            cursor: pointer;
        }

        .value {
            display: flex;
            align-items: center;
            height: 100%;
            width: calc(100% - 45px);
            background-color: white;
            padding: 0 0 0 10px;
        }

        .color {
            height: 100%;
            width: 45px;
            box-sizing: border-box;
            border: 9px solid #E8E8E8;
            background-color: ${hex};
        }
    `;

    return (
        <StyledReadOnly onClick={ clickHandler } className='read-only-container'>
            <div className='value'>
                { hex }
            </div>
            <div className='color'></div>
        </StyledReadOnly>
    );
}

export class ColorsGroup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bottomBarPickerOn: false,
            textPickerOn: false,
            callToActionPickerOn: false,
        };
        this.toggle = this.toggle.bind(this);
    }

    toggle(picker) {
        this.setState({
            [picker]: !this.state[picker]
        });
    }

    render() {
        const { bottomBarHex, textHex, callToActionHex } = this.props;

        const StyledColorsGroup = styled.div`
            .picker {
                overflow: hidden;
                height: 0;

                &.on {
                    height: 250px;
                }
            }
        `;

        return (
            <StyledColorsGroup className='colors-group'>
                <div className='field-label'>Bottom Bar Color</div>
                <ReadOnly hex={bottomBarHex} clickHandler={() => this.toggle('bottomBarPickerOn')} />
                <div className={`picker${this.state.bottomBarPickerOn ? ' on' : ' off'}`}>
                    <Field
                        name      = 'colors.bottomBar'
                        component = {ColorPickerConnected}
                    />
                </div>

                <div className='field-label'>Text Color</div>
                <ReadOnly hex={textHex} clickHandler={() => this.toggle('textPickerOn')} />
                <div className={`picker${this.state.textPickerOn ? ' on' : ' off'}`}>
                    <Field
                        name      = 'colors.text'
                        component = {ColorPickerConnected}
                    />
                </div>

                <div className='field-label'>Call-to-Action Color</div>
                <ReadOnly hex={callToActionHex} clickHandler={() => this.toggle('callToActionPickerOn')} />
                <div className={`picker${this.state.callToActionPickerOn ? ' on' : ' off'}`}>
                    <Field
                        name      = 'colors.callToAction'
                        component = {ColorPickerConnected}
                    />
                </div>
            </StyledColorsGroup>
        );
    }
}

const selector = formValueSelector('settingsForm');

function mapStateToProps(state) {
    const bottomBar    = selector(state, 'colors.bottomBar');
    const text         = selector(state, 'colors.text');
    const callToAction = selector(state, 'colors.callToAction');

    return {
        bottomBarHex:    bottomBar    ? bottomBar.hex    : '#FFFFFF',
        textHex:         text         ? text.hex         : '#FFFFFF',
        callToActionHex: callToAction ? callToAction.hex : '#FFFFFF',
    };
}

export default connect(mapStateToProps)(ColorsGroup);



