import React from 'react';
import styled from 'styled-components';


export default function Button(props) {
    const {
        type,
        id,
        className,
        disabled,
        width,
        height,
        margin,
        background,
        hoverBackground,
        color,
        hoverColor,
        padding,
        clickHandler
    } = props;

    const StyledButton = styled.button`
        width:            ${props.width        ? props.width        : '100%'};
        height:           ${props.height       ? props.height       : '36px'};
        margin:           ${props.margin       ? props.margin       : '0 0 0 0'};
        padding:          ${props.padding      ? props.padding      : '8px'};
        color:            ${props.color        ? props.color        : '#ffffff'};
        background-color: ${props.background   ? props.background   : '#1EB4E2'};
        border-radius:    ${props.borderRadius ? props.borderRadius : '2px'};
        border:           ${props.border       ? props.border       : 'none'};

        text-align: center;
        cursor:     pointer;

        &:focus {
            outline: 0;
            color:            ${props.hoverColor ? props.hoverColor: '#80D9F1'};
            background-color: ${props.hoverBackground ? props.hoverBackground : '#80D9F1'};
        }

        &:hover {
            color:            ${props.hoverColor ? props.hoverColor: '#80D9F1'};
            background-color: ${props.hoverBackground ? props.hoverBackground : '#80D9F1'};
        }
    `;

    return (
        <StyledButton {...props} onClick={clickHandler} type={type ? type : 'button'}>
            { props.children }
        </StyledButton>
    );
}



