import React from 'react';
import styled from 'styled-components';

export default function Demoline({ selected, liValue, type, name, clickHandler}) {
    let classString = (type == selected.type && liValue == selected.id ) ? 'selected' : '';

    return (
        <StyledDemoLine 
            className = { classString }
            onClick   = { () => clickHandler(liValue, type, name)}
        >
            {name}
        </StyledDemoLine>
    );
}

const StyledDemoLine = styled.li`
    border:         none;
    border-radius:  2px;
    list-style:     none;
    font-size:      12px;
    text-transform: capitalize;
    color:          #767676;
    padding:        6px 0 6px 10px;
    cursor:         pointer;
    transition:     all 0.3s 0.05s ease;
    display:        flex;
    align-items:    center;

    &.selected {
        font-weight: bold;
        background-color: #F8F8F8;
    }

    &:hover{
        background-color: #ECECEC;
    }
`;



